import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { register } from '../../../actions/./authAction/auth';
import PropTypes from 'prop-types';
import Alert from '../../layout/Alert';
import { setAlert, remove } from '../../../actions/alert';
import { MenuItem, Select, makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import store from '../../../store';
import { LOAD_ALL_USER_URL, NEW_EMP_CODE_URL } from '../../../apis/apiUrls';
import { Helmet } from 'react-helmet';
import LoadingSpinner from '../../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  inputstyle: {
    background: '#ffffff',
    borderRadius: '10px 10px 0px 0px !important',
    height: '10px',
    padding: '14px',
    borderBottom: '2px solid #000 !important',
    border: 'none !important',
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      backgroundColor: '#0EA900',
    },
  },
}));

const Register = ({ setAlert, register }) => {
  const classes = useStyles();
  const [users, setUsers] = useState([]);
  const [userPage, setUserPage] = useState(0);
  const [code, setCode] = useState(0);
  const alert = useSelector((state) => state.alert);
  const navigate = useNavigate();
  const [showLoader, setShowLoader] = useState(false);
  async function loadMoreItems(event) {
    try {
      if (
        event?.target?.clientHeight + event?.target?.scrollTop + 1 >=
        event?.currentTarget?.scrollHeight
      ) {
        setUserPage((pre) => pre + 1);
      }
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(() => {
    axios.get(`${LOAD_ALL_USER_URL}?role=manager&page=${userPage}&size=20`).then((user) => {
      setUsers((prev) => [...prev, ...user.data.content]);
    });
  }, [userPage]);

  const getEmpCode = () => {
    axios.get(NEW_EMP_CODE_URL).then((code) => {
      setCode(code.data.id);
    });
  };
  const getMaxCurrentDate = () => {
    const today = new Date();
    const year = today.getFullYear();
    let month = today.getMonth() + 1;
    let day = today.getDate();
    month = month < 10 ? '0' + month : month;
    day = day < 10 ? '0' + day : day;
    return `${year}-${month}-${day}`;
  }
  useEffect(() => {
    getEmpCode();
  }, []);
  const originalDate = new Date();
  const year = originalDate.getFullYear();
  const month = (originalDate.getMonth() + 1).toString().padStart(2, '0');
  const day = originalDate.getDate().toString().padStart(2, '0');
  const currentDate = `${year}-${month}-${day}`;
  const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/
  const formik = useFormik({
    initialValues: {
      datepicker: currentDate,
      joinDate: currentDate,
      gender: '',
      firstname: '',
      lastname: '',
      position: '',
      address: '',
      phoneNo: '',
      email: '',
      emergencyContact: '',
      username: '',
      password: '',
      confirmPassword: '',
      manager: '',
      role: '',
    },
    validationSchema: Yup.object({
      email: Yup.string().email('Invalid email').required('Email is required'),
      username: Yup.string().required('username is required'),
      gender: Yup.string().required('Gender is required'),
      role: Yup.string().required('Role is required'),
      datepicker: Yup.string().required('Birthday is required'),
      firstname: Yup.string().required('First name  is required'),
      lastname: Yup.string().required('Last name is required'),
      position: Yup.string().required('Position is required'),
      joinDate: Yup.string().required('Join Date is required'),
      address: Yup.string().required('Address is required'),
      phoneNo: Yup.string().matches(phoneRegExp, 'Phone number is not valid'),
      emergencyContact: Yup.string().matches(phoneRegExp, 'Contact number is not valid'),
      password: Yup.string()
        .min(6, 'Password should be at least 6 characters')
        .required('Password is required'),
      confirmPassword: Yup.string()
        .required('Confirm Password is required')
        .oneOf([Yup.ref('password'), null], 'Password must match'),
    }),
    onSubmit: (values) => {
      setShowLoader(true)
      setTimeout(() => setShowLoader(false), 1000)
      let datepicker = values.datepicker;
      let gender = values.gender;
      let firstname = values.firstname;
      let lastname = values.lastname;
      let position = values.position;
      let address = values.address;
      let phoneNo = values.phoneNo;
      let emergencyContact = values.emergencyContact;
      let manager = values.manager;
      let email = values.email;
      let username = values.username;
      let password = values.password;
      let role = values.role;
      let joinDate = values.joinDate;
      register({
        datepicker,
        joinDate,
        employee: code,
        gender,
        firstname,
        lastname,
        position,
        address,
        phoneNo,
        email,
        emergencyContact,
        username,
        password,
        manager,
        role,
      });
    },
  });
  if (alert[0]?.msg === 'Employee created successfully') {
    store.dispatch(remove(alert[0]?.msg, alert[0]?.alertType, alert[0]?.id));
    navigate('/employee');
  }
  return (
    <>
      <Helmet>
        <title>Add Employee</title>
      </Helmet>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
      <form onSubmit={formik.handleSubmit} id="validation-forms">
        <div className={classes.mainContainer}>
          <div
            style={{
              background: '#FFFFFF',
              boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
              borderRadius: '20px',
              width: '100%',
              marginTop: '0.5%',
              padding: '15px',
  
            }}
          >
            <Grid
              container
              style={{ marginLeft: 'auto', marginRight: 'auto' }}
              xs={12}
              spacing={3}
            >
              <Grid item xs={12}>
                <h3 className='title'>Add Employee </h3>
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Employee ID</label>
                <input
                  id="standard-basic"
                  label="Standard"
                  name="employee"
                  className={
                    formik.touched.employee && formik.errors.employee
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={code}
                  style={{color: '#9E9E9E'}}
                  fullWidth
                  disabled
                />
                {formik.touched.employee && formik.errors.employee ? (
                  <div style={{ color: 'red' }}>{formik.errors.employee}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Gender</label>
                <select
                  name="gender"
                  placeholder="Enter a Description"
                  className={
                    formik.touched.gender && formik.errors.gender
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.gender}
                >
                  <option value="" disabled hidden>
                    Select
                  </option>
                  <option value="Male">Male</option>
                  <option value="Female">Female</option>
                </select>
                {formik.touched.gender && formik.errors.gender ? (
                  <div style={{ color: 'red' }}>{formik.errors.gender}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Birthdate</label>
                {/* <LocalizationProvider dateAdapter={AdapterDateFns}>
                  <MobileDatePicker
                    name="datepicker"
                    onChange={(val) => {
                      formik.setFieldValue('datepicker', val);
                    }}
                    value={formik.values.datepicker}
                    renderInput={(params) => (
                      <TextField
                        className={classes.inputstyle}
                        style={{ width: '100%' }}
                        {...params}
                        InputProps={{ disableUnderline: true }}
                      />
                    )}
                  />
                </LocalizationProvider> */}
                <input
                  id="standard-basic"
                  name="datepicker"
                  label="Standard"
                  type='date'
                  fullWidth
                  onChange={formik.handleChange}
                  className={
                    formik.touched.datepicker && formik.errors.datepicker
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  defaultValue={formik.values.datepicker}
                  value={formik.values.datepicker}
                  max={getMaxCurrentDate()}
                />
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">First Name</label>
                <input
                  id="standard-basic"
                  placeholder="First Name"
                  name="firstname"
                  label="Standard"
                  fullWidth
                  className={
                    formik.touched.firstname && formik.errors.firstname
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.firstname}
                />
                {formik.touched.firstname && formik.errors.firstname ? (
                  <div style={{ color: 'red' }}>{formik.errors.firstname}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Last Name</label>
                <input
                  id="standard-basic"
                  placeholder="Last Name"
                  name="lastname"
                  label="Standard"
                  className={
                    formik.touched.lastname && formik.errors.lastname
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.lastname}
                  fullWidth
                />
                {formik.touched.lastname && formik.errors.lastname ? (
                  <div style={{ color: 'red' }}>{formik.errors.lastname}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Position</label>
                <input
                  id="standard-basic"
                  placeholder="Add Position"
                  label="Standard"
                  name="position"
                  className={
                    formik.touched.position && formik.errors.position
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.position}
                  fullWidth
                />
                {formik.touched.position && formik.errors.position ? (
                  <div style={{ color: 'red' }}>{formik.errors.position}</div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Email</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Email"
                  label="Standard"
                  type='email'
                  name="email"
                  className={
                    formik.touched.email && formik.errors.email
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.email}
                  fullWidth
                />
                {formik.touched.email && formik.errors.email ? (
                  <div style={{ color: 'red' }}>{formik.errors.email}</div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Username</label>
                <input
                  id="standard-basic"
                  placeholder="Enter username"
                  label="Standard"
                  type='text'
                  name="username"
                  className={
                    formik.touched.username && formik.errors.username
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.username}
                  fullWidth
                />
                {formik.touched.username && formik.errors.username ? (
                  <div style={{ color: 'red' }}>{formik.errors.username}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Manager</label>
                <Select
                        name="manager"
                        MenuProps={{
                          PaperProps: {
                            onScroll: loadMoreItems,
                          },
                        }}
                        displayEmpty
                        className={
                          formik.touched.manager && formik.errors.manager
                            ? 'inputstyleTwo'
                            : 'inputstyle'
                        }
                        onChange={formik.handleChange}
                        value={formik.values.manager}
                        disableUnderline
                      >
                        {users?.map((item) => {
                            return (
                              <MenuItem value={item?._id} key={item?._id}>
                                <div
                                  style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                  }}
                                >
                                  <span>
                                    {' '}
                                    {item?.username}
                                  </span>
                                </div>
                              </MenuItem>
                            );
                        })}
                </Select>
                {/* <select
                  name="manager"
                  className={
                    formik.touched.manager && formik.errors.manager
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.manager}
                >
                  <option value="" disabled hidden>
                    Select
                  </option>
                  {users.map(function (user) {
                    return (
                      <option key={user._id} value={user._id}>
                        {user.email}
                      </option>
                    );
                  })}
                </select> */}
                {formik.touched.manager && formik.errors.manager ? (
                  <div style={{ color: 'red' }}>{formik.errors.manager}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Join Date</label>
                {/* <LocalizationProvider dateAdapter={AdapterDateFns}>
                  <MobileDatePicker
                    name="joinDate"
                    onChange={(val) => {
                      formik.setFieldValue('joinDate', val);
                    }}
                    value={formik.values.joinDate}
                    renderInput={(params) => (
                      <TextField
                        className={classes.inputstyle}
                        style={{ width: '100%' }}
                        {...params}
                        InputProps={{ disableUnderline: true }}
                      />
                    )}
                  />
                </LocalizationProvider> */}
                <input
                  id="standard-basic"
                  name="joinDate"
                  label="Standard"
                  type='date'
                  fullWidth
                  onChange={formik.handleChange}
                  className={
                    formik.touched.joinDate && formik.errors.joinDate
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  defaultValue={formik.values.joinDate}
                  value={formik.values.joinDate}
                />
              </Grid>
              <Grid item lg={6} xs={12}>
                <label htmlFor="">Address</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Address"
                  label="Standard"
                  name="address"
                  fullWidth
                  className={
                    formik.touched.address && formik.errors.address
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.address}
                />
                {formik.touched.address && formik.errors.address ? (
                  <div style={{ color: 'red' }}>{formik.errors.address}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Phone No</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Phone No"
                  label="Standard"
                  name="phoneNo"
                  fullWidth
                  className={
                    formik.touched.phoneNo && formik.errors.phoneNo
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.phoneNo}
                />
                {formik.touched.phoneNo && formik.errors.phoneNo ? (
                  <div style={{ color: 'red' }}>{formik.errors.phoneNo}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Emergency Contact</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Emergency Contact"
                  label="Standard"
                  name="emergencyContact"
                  fullWidth
                  className={
                    formik.touched.emergencyContact && formik.errors.emergencyContact
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.emergencyContact}
                />
                {formik.touched.emergencyContact && formik.errors.emergencyContact ? (
                  <div style={{ color: 'red' }}>{formik.errors.emergencyContact}</div>
                ) : null}
              </Grid>
              <Grid item lg={3} xs={12} md={3}>
                <label htmlFor="">Role</label>
                <select
                  name="role"
                  className={
                    formik.touched.role && formik.errors.role
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.role}
                >
                  <option value="" disabled hidden>
                    Select
                  </option>
                  <option value="user">User</option>
                  <option value="manager">Manager</option>
                  <option value="admin">Admin</option>
                </select>
                {formik.touched.role && formik.errors.role ? (
                  <div style={{ color: 'red' }}>{formik.errors.role}</div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Password</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Password"
                  label="Standard"
                  type="password"
                  name="password"
                  fullWidth
                  className={
                    formik.touched.password && formik.errors.password
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.password}
                />
                {formik.touched.password && formik.errors.password ? (
                  <div style={{ color: 'red' }}>{formik.errors.password}</div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Confirm Password</label>
                <input
                  id="standard-basic"
                  placeholder="Confirm Password"
                  label="Standard"
                  type="password"
                  name="confirmPassword"
                  className={
                    formik.touched.confirmPassword &&
                    formik.errors.confirmPassword
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.confirmPassword}
                  fullWidth
                />
                {formik.touched.confirmPassword &&
                formik.errors.confirmPassword ? (
                  <div style={{ color: 'red' }}>
                    {formik.errors.confirmPassword}
                  </div>
                ) : null}
              </Grid>
              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
                {alert.length === 1 ? <Alert /> : null}
                <Button
                  variant="contained"
                  className={classes.btn}
                  color="secondary"
                  type="submit"
                  disabled={alert.length > 0 ? true : false}
                >ADD           
                </Button>
              </Grid>
            </Grid>
          </div>
          <br />
          <br />  
        </div>
      </form>
      }
    </>
  );
};

Register.propTypes = {
  setAlert: PropTypes.func.isRequired,
  register: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { setAlert, register })(Register);
