import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { register } from '../../../actions/./authAction/auth';
import PropTypes from 'prop-types';
import Alert from '../../layout/Alert';
import { setAlert } from '../../../actions/alert';
import { MenuItem, Select, makeStyles } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import axios from 'axios';

import {
  LOAD_ALL_USER_URL,
  LOAD_USER_URL,
  UPDATE_EMPLOYEE_URL,
} from '../../../apis/apiUrls';
import LoadingSpinner from '../../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      //you want this to be the same as the backgroundColor above
      backgroundColor: '#0EA900',
    },
  },
  inputstyle: {
    background: '#ffffff',
    borderRadius: '10px 10px 0px 0px !important',
    height: '50px',
    padding: '14px',
    borderBottom: '2px solid #000 !important',
    border: 'none !important',
  },
}));

const EmployeeUpdate = ({ setAlert, register }) => {
  const [formData, setFormData] = useState({
    datepicker: null,
    address: '',
    email: '',
    emergencyContact: '',
    username: '',
    employee: '',
    firstname: '',
    gender: '',
    lastname: '',
    manager: '',
    password: '',
    phoneNo: '',
    position: '',
    joinDate: null,
  });
  const params = useParams();
  const navigate = useNavigate();
  const [showLoader, setShowLoader] = useState(false);

  const {
    datepicker,
    address,
    email,
    emergencyContact,
    username,
    employee,
    firstname,
    gender,
    lastname,
    manager,
    role,
    phoneNo,
    position,
    joinDate,
  } = formData;
  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });
  const [users, setUsers] = useState([]);
  const [userPage, setUserPage] = useState(0);
  async function loadMoreItems(event) {
    try {
      if (
        event?.target?.clientHeight + event?.target?.scrollTop + 1 >=
        event?.currentTarget?.scrollHeight
      ) {
        setUserPage((pre) => pre + 1);
      }
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(() => {
    axios.get(`${LOAD_ALL_USER_URL}?role=manager&page=${userPage}&size=20`).then((user) => {
      setUsers((prev) => [...prev, ...user.data.content]);
    });
  }, [userPage]);

  const classes = useStyles();

  const getUser = (userid) => {
    axios.get(`${LOAD_USER_URL}/${userid}`).then((res) => {
      if(res.data.datepicker!=null){
        const originalDate = new Date(res.data.datepicker);
        const year = originalDate.getFullYear();
        const month = (originalDate.getMonth() + 1).toString().padStart(2, '0');
        const day = originalDate.getDate().toString().padStart(2, '0');
        res.data.datepicker = `${year}-${month}-${day}`;
      }
      if(res.data.joinDate!=null){
        const originalDate = new Date(res.data.joinDate);
        const year = originalDate.getFullYear();
        const month = (originalDate.getMonth() + 1).toString().padStart(2, '0');
        const day = originalDate.getDate().toString().padStart(2, '0');
        res.data.joinDate = `${year}-${month}-${day}`;
      }
      setFormData(res.data);
    });
  };
  useEffect(() => {
    getUser(params.id);
  }, [params.id]);
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };

  const Handlesubmit = (e) => {
    e.preventDefault();
    setShowLoader(true)
    setTimeout(() => setShowLoader(false), 1000)
    axios
      .patch(`${UPDATE_EMPLOYEE_URL}/${params.id}`, formData, config)
      .then((res) => {
        navigate(-1);
      })
      .catch((error) => {});
  };
  return (
    <div className={classes.mainContainer}>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
      <div
            style={{
              background: '#FFFFFF',
              boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
              borderRadius: '20px',
              width: '100%',
              marginTop: '0.5%',
              padding: '15px',
  
            }}
      >
        <Grid
          container
          style={{ marginLeft: 'auto', marginRight: 'auto' }}
          xs={12}
          spacing={3}
        >
          <Grid item xs={12}>
                <h3 className='title'>Update Employee </h3>
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Employee ID</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Emp Code"
              label="Standard"
              value={employee}
              name="employee"
              onChange={(e) => onChange(e)}
              style={{color: '#9E9E9E'}}
              fullWidth
              disabled
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Gender</label>
            <select
              className="inputstyle"
              value={gender}
              name="gender"
              onChange={(e) => onChange(e)}
            >
              <option value="" disabled hidden>
                Select
              </option>
              <option>Male</option>
              <option>Female</option>
            </select>
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Birthdate</label>
            {/* <LocalizationProvider dateAdapter={AdapterDateFns}>
              <Stack spacing={2}>
                <MobileDatePicker
                  name="datepicker"
                  value={new Date(datepicker)}
                  onChange={(newValue) => {
                    setFormData({ ...formData, datepicker: newValue });
                  }}
                  renderInput={(params) => (
                    <TextField
                      className={classes.inputstyle}
                      style={{ width: '100%' }}
                      {...params}
                      InputProps={{ disableUnderline: true }}
                    />
                  )}
                />
              </Stack>
            </LocalizationProvider>{' '} */}
               <input
                  id="standard-basic"
                  name="datepicker"
                  label="Standard"
                  type='date'
                  className="inputstyle"
                  fullWidth
                  onChange={(e) => onChange(e)}
                  defaultValue={datepicker}
                  value={datepicker}
                />

          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">First Name</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="First Name"
              name="firstname"
              label="Standard"
              value={firstname}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Last Name</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Last Name"
              name="lastname"
              label="Standard"
              value={lastname}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Position</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Add Position"
              name="position"
              label="Standard"
              value={position}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Email</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Email"
              name="email"
              label="Standard"
              value={email}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Username</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter username"
              name="username"
              label="Standard"
              value={username}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Manager</label>
            <Select
                        name="manager"
                        MenuProps={{
                          PaperProps: {
                            onScroll: loadMoreItems,
                          },
                        }}
                        displayEmpty
                        className='inputstyle'
                        value={manager}
                        onChange={(e) => onChange(e)}
                        disableUnderline
                      >
                        {users?.map((item) => {
                            return (
                              <MenuItem value={item?._id} key={item?._id} selected={manager}>
                                <div
                                  style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                  }}
                                >
                                  <span>
                                    {' '}
                                    {item?.username}
                                  </span>
                                </div>
                              </MenuItem>
                            );
                        })}
                  </Select>
            {/* <select
              className="inputstyle"
              value={manager}
              name="manager"
              onChange={(e) => onChange(e)}
            >
              <option value="" disabled hidden>
                Select
              </option>
              {users.map(function (user) {
                return (
                  <option key={user._id} value={user._id} selected={manager}>
                    {user.email}
                  </option>
                );
              })}
            </select> */}
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Join Date</label>
            {/* <LocalizationProvider dateAdapter={AdapterDateFns}>
              <Stack spacing={2}>
                <MobileDatePicker
                  name="joinDate"
                  value={new Date(joinDate)}
                  onChange={(newValue) => {
                    setFormData({ ...formData, joinDate: newValue });
                  }}
                  renderInput={(params) => (
                    <TextField
                      className={classes.inputstyle}
                      style={{ width: '100%' }}
                      {...params}
                      InputProps={{ disableUnderline: true }}
                    />
                  )}
                />
              </Stack>
            </LocalizationProvider> */}
                <input
                  id="standard-basic"
                  name="joinDate"
                  label="Standard"
                  type='date'
                  className="inputstyle"
                  fullWidth
                  onChange={(e) => onChange(e)}
                  defaultValue={joinDate}
                  value={joinDate}
                />
              {' '}
          </Grid>
          <Grid item lg={6} xs={12}>
            <label htmlFor="">Address</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Address"
              label="Standard"
              value={address}
              name="address"
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={6}>
            <label htmlFor="">Phone No</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Phone No"
              name="phoneNo"
              label="Standard"
              value={phoneNo}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={6}>
            <label htmlFor="">Emergency Contact </label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Emergency Contact "
              name="emergencyContact"
              label="Standard"
              value={emergencyContact}
              onChange={(e) => onChange(e)}
              fullWidth
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Role</label>
            <select
              name="role"
              className="inputstyle"
              value={role}
              onChange={(e) => onChange(e)}
            >
              <option value="" disabled hidden>
                Select
              </option>
              <option value="user">User</option>
              <option value="manager">Manager</option>
              <option value="admin">Admin</option>
            </select>
          </Grid>{' '}
          <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
            <Alert />
            <Button
              variant="contained"
              className={classes.btn}
              color="secondary"
              onClick={Handlesubmit}
            >
              Update
            </Button>
          </Grid>
        </Grid>
      </div>
      }
    </div>
  );
};

EmployeeUpdate.propTypes = {
  setAlert: PropTypes.func.isRequired,
  register: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { setAlert, register })(EmployeeUpdate);
