import axios from "axios";

import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    USER_LOADED,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT,
    UPDATE_PASSWORD,
    USER_All_LOADED,
    LOGIN_VERIFY_FAIL,
} from "../types";
import setAuthToken from "../../utils/setAuthToken";
import { setAlert } from "../alert";
import { LOGIN_URL, LOAD_USER_URL, LOAD_ALL_USER_URL, REGISTER_URL, UPDATE_PASSWORD_URL } from "../../apis/apiUrls";

// Load User
export const loadUser = () => async (dispatch) => {
    if (localStorage.token) {
        setAuthToken(localStorage.token);
    }

    try {
        const res = await axios.get(LOAD_USER_URL);

        dispatch({
            type: USER_LOADED,
            payload: res.data,
        });
    } catch (err) {
        dispatch({
            type: AUTH_ERROR,
        });
    }
};
export const loadAllUser = () => async (dispatch) => {
    if (localStorage.token) {
        setAuthToken(localStorage.token);
    }

    try {
        const res = await axios.get(LOAD_ALL_USER_URL);
        dispatch({
            type: USER_All_LOADED,
            payload: res.data.content,
        });
    } catch (err) {
        dispatch({
            type: AUTH_ERROR,
        });
    }
};

// Register User
export const register = ({ datepicker,
    employee,
    joinDate,
    gender,
    firstname,
    lastname,
    position,
    address,
    phoneNo,
    email,
    emergencyContact,
    username,
    password,
    manager,
    role }) =>
    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };

        const body = JSON.stringify({
            datepicker,
            employee,
            gender,
            firstname,
            lastname,
            position,
            address,
            phoneNo,
            email,
            emergencyContact,
            username,
            password,
            manager,
            joinDate,
            role
        });

        try {
            const res = await axios.post(
                REGISTER_URL,
                body,
                config
            );
            console.log(res)
            dispatch({
                type: REGISTER_SUCCESS,
                payload: res.data,
            });
            if (res.status === 201) {
                dispatch(setAlert("Employee created successfully", "success"))
            }

            dispatch(loadUser());
        } catch (err) {
            console.log(err);
            const errors = err.response.data.errors;

            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }

            dispatch({
                type: REGISTER_FAIL,
            });
        }
    };

// Login User


export const login = (username, password) => async (dispatch) => {
    const config = {
        headers: {
            "Content-Type": "application/json",
        },
    };

    const body = JSON.stringify({ username, password });

    try {
        const res = await axios.post(
            LOGIN_URL,
            body,
            config
        );
        if (res.data?.verified === true) {
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data,
            });
            dispatch(loadUser());
        }else{
            dispatch({
                type: LOGIN_VERIFY_FAIL,
                payload: res.data
            });
        }
        
    } catch (err) {
        const errors = err.response.data.errors;

        if (errors) {
            errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
        }

        dispatch({
            type: LOGIN_FAIL,
        });
    }
};


