//Update Password
export const updatepassword = ({ password, currentPassword, username }) =>
    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };

        const body = JSON.stringify({ password, currentPassword, username });

        try {
            const res = await axios.patch(
                UPDATE_PASSWORD_URL,
                body,
                config
            );


            dispatch({
                type: UPDATE_PASSWORD,
            });
            dispatch({
                type: LOGIN_SUCCESS,
                payload: res.data,
            });
            dispatch(loadUser());
        } catch (err) {
            const errors = err.response.data.errors;
            console.log(err);
            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }
            dispatch({
                type: REGISTER_FAIL,
            });
        }
    };

// Logout / Clear Profile

export const logout = () => (dispatch) => {
    dispatch({ type: LOGOUT });
};