import './Landing.css';
import Header from './Header';

const Landing = () => {
  return (
    <>
      <div className="landing-background">
        {/* header */}
        <Header isLanding={true} />
        {/* body */}
        <div>
          <div className="text-welcome">Welcome to</div>
          <div className="text-employee-management">MyWorkPlace</div>
        </div>
      </div>
    </>
  );
};

export default Landing;
