import React, { useState, useEffect } from "react";
import { MenuItem, Select, makeStyles } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { useParams } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import PropTypes from "prop-types";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableContainer from "@material-ui/core/TableContainer";
import { getLeaveType } from "../../../actions/adminLeaveAction";
import store from "../../../store";
import axios from "axios";
import {
  LOAD_USER_URL,
  GET_MANAGER_URL,
  LEAVE_COUNT_URL,
} from "../../../apis/apiUrls";
const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    height: "100vh",
    borderRadius: "0px",
    background: "#F5F5F5",
    marginTop: "0.5%",
    // // boxShadow: '5.29353px 0px 13.2338px rgba(0, 0, 0, 0.2)',
  },
  mainContainer: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    width: "90%",
    height: "100vh",
    marginLeft: "auto",
    marginTop: "2%",
    marginRight: "auto",
    background: "#F5F5F5",
    [theme.breakpoints.down("xs")]: {
      marginTop: "20%",
    },
  },
  paper: {
    background: "#F5F5F5",
  },
  bodyContainer: {
    display: "flex",
    justifyContent: "space-between",
    marginTop: "1%",
    marginLeft: "auto",
    marginRight: "auto",
    width: "96%",
  },
  inputstyle: {
    background: "#ffffff",
    borderRadius: "10px 10px 0px 0px !important",
    height: "10px",
    padding: "14px",
    borderBottom: "2px solid #000 !important",
    border: "none !important",
  },
}));

const styles = (theme) => ({
  rootTable: {
    width: "100%",
    marginTop: theme.spacing.unit * 3,
    overflowX: "auto",
    overflowY: "auto",
  },
  table: {
    minWidth: 100,
  },
});


function EmployeeDetail() {
  const classes = useStyles();
  const [managerUsername, setManagerUsername] = useState([]);
  const [user, setUser] = useState({ manager: "" });
  const [leaveCount, setLeaveCount] = useState([]);
  const [years, setYears] = useState([new Date().getFullYear()]);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
  const { id } = useParams();
  const createYearList = (startYear, endYear) => {
    const yearList = [];
    for (let year = startYear; year <= endYear; year++) {
      yearList.push(year);
    }
    return yearList;
  };
  useEffect(() => {
    axios.get(`${LOAD_USER_URL}/${id}`).then((res) => {
      setUser(res?.data);
      const joinedYear = new Date(res?.data?.joinDate).getFullYear();
      const currentYear = new Date().getFullYear();
      const yearList = createYearList(joinedYear, currentYear);
      setYears(yearList);
    });
  }, [id]);
  useEffect(() => {
    store.dispatch(getLeaveType());
  }, []);
  useEffect(() => {
    axios
      .get(`${GET_MANAGER_URL}?id=${user?.manager}&type=single`)
      .then((res) => {
        setManagerUsername(res?.data?.username);
      })
      .catch((error) => {
        console.log(error);
      });
  }, [user?.manager]);
  useEffect(() => {
    if (user._id) {
      axios.get(`${LEAVE_COUNT_URL}/${user._id}?year=${selectedYear}`).then((res) => {
        setLeaveCount(res?.data);
      });
    }
  }, [user._id, selectedYear]);
  const onChangeYear = (e) => setSelectedYear(e.target.value);

  return (
    <div className={classes.mainContainer}>
      <div
        style={{
          background: "#FFFFFF",
          boxShadow: "-1px 0px 20px 1px rgba(0, 0, 0, 0.1)",
          borderRadius: "20px",
          width: "100%",
          marginTop: "0.5%",
          padding: "15px",
        }}
      >
        <Grid
          container
          style={{ marginLeft: "auto", marginRight: "auto" }}
          xs={12}
          spacing={3}
        >
          <Grid item xs={12}>
            <h3 className="title">Employee Details</h3>
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Employee ID</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Emp Code"
              label="Standard"
              value={user.employee}
              name="employee"
              style={{ color: "#9E9E9E" }}
              fullWidth
              disabled
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Gender</label>
            <select
              className="inputstyle"
              value={user.gender}
              name="gender"
              disabled
            >
              <option>Male</option>
              <option>Female</option>
            </select>
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Birthdate</label>
            <input
              id="standard-basic"
              name="datepicker"
              label="Standard"
              className="inputstyle"
              fullWidth
              value={user?.datepicker
                ? new Intl.DateTimeFormat('en-GB', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date(user.datepicker))
                : 'N/A'}
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">First Name</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="First Name"
              name="firstname"
              label="Standard"
              value={user.firstname}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Last Name</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Last Name"
              name="lastname"
              label="Standard"
              value={user.lastname}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Position</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Add Position"
              name="position"
              label="Standard"
              value={user.position}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Email</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Email"
              name="email"
              label="Standard"
              value={user.email}
              fullWidth
              disabled
              style={{color: '#9E9E9E'}}
            />
          </Grid>
          <Grid item lg={6} xs={12} md={6}>
            <label htmlFor="">Username</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Username"
              name="username"
              label="Standard"
              value={user.username}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Manager</label>
            <input
              id="standard-basic"
              className="inputstyle"
              name="manager"
              label="Standard"
              value={managerUsername}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Join Date</label>
            <input
              id="standard-basic"
              name="joinDate"
              label="Standard"
              className="inputstyle"
              fullWidth
              value={user?.joinDate
                ? new Intl.DateTimeFormat('en-GB', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date(user.joinDate))
                : 'N/A'}
              disabled
              style={{ color: "#9E9E9E" }}
            />{" "}
          </Grid>
          <Grid item lg={6} xs={12}>
            <label htmlFor="">Address</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Address"
              label="Standard"
              value={user.address}
              name="address"
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={6}>
            <label htmlFor="">Phone No</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Phone No"
              name="phoneNo"
              label="Standard"
              value={user.phoneNo}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={6}>
            <label htmlFor="">Emergency Contact</label>
            <input
              id="standard-basic"
              className="inputstyle"
              placeholder="Enter Emergency Contact"
              name="emergencyContact"
              label="Standard"
              value={user.emergencyContact}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
          <Grid item lg={3} xs={12} md={3}>
            <label htmlFor="">Role</label>
            <input
              name="role"
              className="inputstyle"
              value={user.role}
              fullWidth
              disabled
              style={{ color: "#9E9E9E" }}
            />
          </Grid>
        </Grid>
      </div>
      <br />
      <br />
      {user.role !== "admin" ? (
        <div
          style={{
            width: "100%",
            marginTop: "0.5%",
            padding: "15px",
          }}
        >
          <Grid item xs={12}>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h2 className="title">Leave Details</h2>
              <Grid item lg={3} xs={12} md={3}>
              <Select
                name="year"
                displayEmpty
                className={"inputstyle"}
                onChange={(e) => onChangeYear(e)}
                value={selectedYear}
                disableUnderline
              >
                {years?.map((item) => {
                  return (
                    <MenuItem value={item}>
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                        }}
                      >
                        <span> Year - {item} </span>
                      </div>
                    </MenuItem>
                  );
                })}
              </Select>
              </Grid>
            </div>
          </Grid>
          <br />
          {leaveCount.length > 0 ?
          <TableContainer
            className={classes.rootTable}
            style={{
              // marginLeft: 'auto',
              // marginRight: 'auto',
              background: "#FFFFFF",
              boxShadow: "-1px 0px 20px 1px rgba(0, 0, 0, 0.1)",
              borderRadius: "20px",
              width: "100%",
            }}
          >
            <Table
              className={classes.table}
              style={{ maxWidth: "90vw", overflow: "auto" }}
            >
              <TableHead>
                <TableRow>
                  <TableCell style={{ borderBottom: "2px solid gray" }}>
                    Leave Type
                  </TableCell>
                  <TableCell style={{ borderBottom: "2px solid gray" }}>
                    Total Leave
                  </TableCell>
                  <TableCell style={{ borderBottom: "2px solid gray" }}>
                    Used Leave
                  </TableCell>
                  <TableCell style={{ borderBottom: "2px solid gray" }}>
                    Available Leave
                  </TableCell>
                  <TableCell style={{ borderBottom: "2px solid gray" }}>
                    Pending Leave
                  </TableCell>
                  <TableCell style={{ borderBottom: '2px solid gray' }}>
                    Previous Year Leave
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {leaveCount?.map((row) => (
                  <TableRow key={row?.leaveType}>
                    <TableCell
                      style={{ borderBottom: "none" }}
                      component="th"
                      scope="row"
                    >
                      {row?.leaveType}
                    </TableCell>
                    <TableCell style={{ borderBottom: "none" }}>
                      {row?.numberLeave}
                    </TableCell>
                    <TableCell style={{ borderBottom: "none" }}>
                      {row?.usedLeave}
                    </TableCell>
                    <TableCell style={{ borderBottom: "none" }}>
                      {row?.availableLeave}
                    </TableCell>
                    <TableCell style={{ borderBottom: "none" }}>
                      {row?.appliedLeave}
                    </TableCell>
                    <TableCell style={{ borderBottom: 'none' }}>
                          {row?.previousLeave}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          : <div
          style={{
            background: "#FFFFFF",
            boxShadow: "-1px 0px 20px 1px rgba(0, 0, 0, 0.1)",
            borderRadius: "20px",
            width: "100%",
            height: '200px',
            paddingTop: '100px',
            display: 'flex',
            justifyContent: 'center'
          }}>
            No leaves history found for this year
          </div> }
          <br />
        </div>
      ) : null}
    </div>
  );
}

EmployeeDetail.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(EmployeeDetail);
