import React, { useState, useEffect } from 'react';
import { MenuItem, Select, makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { useParams } from 'react-router-dom';
import TableContainer from '@material-ui/core/TableContainer';
import { getLeaveType } from '../../../actions/adminLeaveAction';
import store from '../../../store';
import axios from 'axios';
import { getLeave } from '../../../actions/leaveAction';
import Modal from './Modal';
import { LEAVE_COUNT_URL, LEAVE_URL } from '../../../apis/apiUrls';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100vh',
    borderRadius: '0px',
    background: '#F5F5F5',
    marginTop: '0.5%',
    // // boxShadow: '5.29353px 0px 13.2338px rgba(0, 0, 0, 0.2)',
  },
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
    background: '#F5F5F5'
  },
  paper: {
    background: '#F5F5F5',
  },
  bodyContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    marginTop: '2%',
    marginLeft: 'auto',
    marginRight: 'auto',
    width: '100%',
    background: '#F5F5F5'
  },
  btn: {
    [theme.breakpoints.down('sm')]: {
      paddingTop: '20px',
    },
  },
}));

function LeaveDetail({auth: { user }}) {
  const classes = useStyles();
  const [data, setData] = useState({});
  const [update, setupdate] = useState(false);
  const [leaveCount, setLeaveCount] = useState([]);
  const [years, setYears] = useState([new Date().getFullYear()]);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
  const { id } = useParams();
  useEffect(() => {
    store.dispatch(getLeaveType());
    store.dispatch(getLeave());
    getLeaveById(id);
  }, [update, id]);

  const getLeaveById = async (id) => {
    await axios.get(`${LEAVE_URL}/${id}`).then((res) => {
      setData(res?.data);
    });
  };
  useEffect(() => {
    if (data?.userid) {
      axios.get(`${LEAVE_COUNT_URL}/${data.userid}?year=${selectedYear}`).then((res) => {
        setLeaveCount(res.data);
      });
    }
    const joinedYear = new Date(data?.joinDate).getFullYear();
    const currentYear = new Date().getFullYear();
    const yearList = createYearList(joinedYear, currentYear);
    setYears(yearList);
  }, [data?.userid, update, selectedYear, data]);
  const createYearList = (startYear, endYear) => {
    const yearList = [];
    for (let year = startYear; year <= endYear; year++) {
      yearList.push(year);
    }
    return yearList;
  };
  const onChangeYear = (e) => setSelectedYear(e.target.value);
  return (
    <>
      <Helmet>
        <title>Leave Details</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <h3 style={{ marginTop: '5px',fontFamily: "Roboto",
            fontSize: '34px',
            fontWeight: '700',
            lineHeight: '39.84px',  }} className="title">
          Leave Details
        </h3>
        <div
          style={{
            width: '100%',
            borderRadius: '0px',
            background: '#F5F5F5',
            marginTop: '0.5%',
            // // boxShadow: '5.29353px 0px 13.2338px rgba(0, 0, 0, 0.2)',
          }}
        >
          <br />
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={6}
              sm={12}
              md={6}
            >
              <Grid xs={4} style={{ fontWeight: 'bold' }}>
                Emp Name :{' '}
              </Grid>
              <Grid xs={7}>{data?.name} </Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={6}
              sm={12}
              md={6}
            >
              <Grid xs={4} style={{ fontWeight: 'bold' }}>
                Emp ID :{' '}
              </Grid>
              <Grid xs={7}>{data?.employee} </Grid>
            </Grid>
          </Grid>
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={6}
              sm={12}
              md={6}
            >
              <Grid xs={4} style={{ fontWeight: 'bold' }}>
                Emp Username :
              </Grid>
              <Grid xs={7} style={{ overflowWrap: 'anywhere' }}>
                {data?.username}{' '}
              </Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={6}
              sm={12}
              md={6}
            >
              <Grid xs={4} style={{ fontWeight: 'bold' }}>
                Phone No :{' '}
              </Grid>
              <Grid xs={7}>{data?.phoneNo} </Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            ></Grid>
          </Grid>
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Leave Type :{' '}
              </Grid>
              <Grid xs={6}>{data?.leaveCategory} </Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Start Date :{' '}
              </Grid>
              <Grid xs={6}>{data?.startDate}</Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                End Date :{' '}
              </Grid>
              <Grid xs={6}>{data?.endDate}</Grid>
            </Grid>
          </Grid>
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Posting date :{' '}
              </Grid>
              <Grid xs={6}> {data?.date}</Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Number of Days :{' '}
              </Grid>
              <Grid xs={6}>{data?.noOfDays}</Grid>
            </Grid>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Half Day :{' '}
              </Grid>
              <Grid xs={6}>{data?.halfDay ? 'Yes' : 'No'}</Grid>
            </Grid>
            
          </Grid>
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={12}
              sm={12}
              md={6}
            >
              <Grid xs={12} lg={2} style={{ fontWeight: 'bold' }}>
              Leave Description :{' '}
              </Grid>
              <Grid xs={12} lg={9} style={{ overflowWrap: 'anywhere' }}>
                {data?.leaveDescription}{' '}
              </Grid>
            </Grid>
          </Grid>
          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={12}
              sm={12}
              md={6}
            >
              <Grid xs={12} lg={2} style={{ fontWeight: 'bold' }}>
                Leave Status :{' '}
              </Grid>
              <Grid
                xs={12}
                lg={2}
                style={{
                  color:
                    data?.status === 'Pending'
                      ? '#EDBE17'
                      : data?.status === 'Granted'
                      ? '#0EA900'
                      : 'red',
                }}
              >
                {data?.status}{' '}
              </Grid>
            </Grid>
          </Grid>
          {data?.adminActionDate !== '' ? (
            <Grid container className={classes.bodyContainer}>
              <Grid
                style={{
                  display: 'flex',
                }}
                xs={12}
                lg={4}
                sm={12}
                md={6}
              >
                <Grid xs={6} style={{ fontWeight: 'bold' }}>
                  Admin Action Date :{' '}
                </Grid>
                <Grid xs={6}>{data?.adminActionDate} </Grid>
              </Grid>
            </Grid>
          ) : null}

          <Grid container className={classes.bodyContainer}>
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid xs={6} style={{ fontWeight: 'bold' }}>
                Admin Remark :{' '}
              </Grid>
              <Grid xs={6}>{data?.adminRemark} </Grid>
            </Grid>
            { user?._id !== data?.userid && data?.status === 'Pending' ?
            <Grid
              style={{
                display: 'flex',
              }}
              xs={12}
              lg={4}
              sm={12}
              md={6}
            >
              <Grid
                xs={6}
                style={{ fontWeight: 'bold' }}
                className={classes.btn}
              >
                <Modal
                  id={id}
                  setupdate={setupdate}
                  update={update}
                  statusServer={data?.status}
                />
              </Grid>
            </Grid> : ''}
          </Grid>
          <br />
          <br />
          <div
            style={{
              width: '100%',
              marginTop: '0.5%',
              padding: '15px',
  
            }}
      >            
          <Grid item xs={12}>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <h2 className="title">Leave History</h2>
              <Grid item lg={3} xs={12} md={3}>
              <Select
                name="year"
                displayEmpty
                className={"inputstyle"}
                onChange={(e) => onChangeYear(e)}
                value={selectedYear}
                disableUnderline
              >
                {years?.map((item) => {
                  return (
                    <MenuItem value={item}>
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                        }}
                      >
                        <span> Year - {item} </span>
                      </div>
                    </MenuItem>
                  );
                })}
              </Select>
              </Grid>
            </div>
          </Grid>
            <br />
            {leaveCount.length > 0 ?
            <TableContainer
                  className={classes.rootTable}
                  style={{           
                    // marginLeft: 'auto',
                    // marginRight: 'auto',
                    background: '#FFFFFF',
                    boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
                    borderRadius: '20px',
                    width: '100%',
                    }}
                >
                  <Table
                    className={classes.table}
                    style={{ maxWidth: '90vw', overflow: 'auto' }}
                  >
                    <TableHead>
                      <TableRow>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Leave Type
                      </TableCell>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Total Leave
                      </TableCell>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Used Leave
                      </TableCell>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Available Leave
                      </TableCell>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Pending Leave
                      </TableCell>
                      <TableCell style={{ borderBottom: '2px solid gray' }}>
                        Previous Year Leave
                      </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                    {leaveCount?.map((row) => (
                      <TableRow key={row?.leaveType}>
                        <TableCell
                          style={{ borderBottom: 'none' }}
                          component="th"
                          scope="row"
                        >
                          {row?.leaveType}
                        </TableCell>
                        <TableCell style={{ borderBottom: 'none' }}>
                          {row?.numberLeave}
                        </TableCell>
                        <TableCell style={{ borderBottom: 'none' }}>
                          {row?.usedLeave}
                        </TableCell>
                        <TableCell style={{ borderBottom: 'none' }}>
                          {row?.availableLeave}
                        </TableCell>
                        <TableCell style={{ borderBottom: 'none' }}>
                          {row?.appliedLeave}
                        </TableCell>
                        <TableCell style={{ borderBottom: 'none' }}>
                          {row?.previousLeave}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>

                  </Table>
                </TableContainer>
                : <div
                style={{
                  background: "#FFFFFF",
                  boxShadow: "-1px 0px 20px 1px rgba(0, 0, 0, 0.1)",
                  borderRadius: "20px",
                  width: "100%",
                  height: '200px',
                  paddingTop: '100px',
                  display: 'flex',
                  justifyContent: 'center'
                }}>
                  No leaves history found for this year
                </div> }
                <br />
          </div>
          <br />
        </div>
        <br />
        <br />
      </div>
    </>
  );
}

LeaveDetail.propTypes = {
  classes: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps)(LeaveDetail);