import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Grid from '@material-ui/core/Grid';
import TablePagination from '@material-ui/core/TablePagination';
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import LaunchIcon from '@mui/icons-material/Launch';
import IconButton from '@material-ui/core/IconButton';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { LOAD_USER_URL } from '../../../apis/apiUrls';

const useStyles = makeStyles({
  table: {
    minWidth: '90%',
  },
  delete: {
    color: '#ff0011',
    //   marginLeft:45
  },
  button1: {
    width: 20,
    height: 20,
    padding: 0,
  },
  button2: {
    width: 40,
    height: 20,
    padding: 0,
  },
  icon: {
    width: 64,
    height: 64,
  },
  MuiTableRowroot: {
    fontWeight: 'bold !important',
    fontSize: '17px !important',
  },
});

function EmployeeTable({ 
  data,
  setSize,
  setPageNumber,
  totalElements,
  setData,
  setTotalElements,
  setSearchKeyword,
  searchKeyword, 
}) {
  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  const handleChangePage = (event, newPage) => {
    setPageNumber(newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPageNumber(0);
    setPage(0);
    setRowsPerPage(event.target.value);
    setSize(event.target.value);
  };
  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, totalElements - page * rowsPerPage);
  
    const labelDisplayedRows = ({ from, to, count }) => {
      return `Showing ${from}-${to} of ${count} entries`;
    };  
  const HandleDeleteOne = (id) => {
    axios.delete(`${LOAD_USER_URL}/${id}`).then((res) => {
      setData((pre) => pre?.filter((item) => item._id !== id));
      setTotalElements((pre) => pre - 1);
    });
  };
  return (
    <Grid
      style={{
        marginLeft: 'auto',
        marginRight: 'auto',
        paddingBottom: '1%',
      }}
      xs={11}
    >
      <h3 className='title'>Manage Employee</h3>
      <br />
      <br />
      <Grid
        spacing={1}
        container
        xs={12}
        style={{ display: 'flex', justifyContent: 'space-between' }}
      >
        <Grid item lg={9} xs={12} md={9} sm={12}>
          {' '}
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            className="page"
            count={totalElements}
            style={{
              display: 'flex',
              width: '100% !important',
            }}
            rowsPerPage={rowsPerPage}
            labelDisplayedRows={labelDisplayedRows}
            page={page}
            SelectProps={{
              SelectDisplayProps: {
                style: { alignSelf: 'flex-end' },
              },
            }}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Grid>
        <Grid item lg={3} xs={12} md={4} sm={12}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <input
              onChange={(e) => setSearchKeyword(e.target.value)}
              value={searchKeyword}
              id="standard-basic"
              placeholder="Search"
              label="Standard"
              name="search"
              className="inputstyle-two"
              fullWidth  
            />
            <div class="icon-container">
              <img
                src="/search.svg"
                alt=""
                style={{
                  width: '22px',
                  marginLeft: '-50px',
                }}
              />
            </div>
          </div>
        </Grid>
      </Grid>
      <br />
      <TableContainer
        className={classes.rootTable}
        style={{
          marginLeft: 'auto',
          marginRight: 'auto',
          background: '#FFFFFF',
          boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
          borderRadius: '20px',
          width: '100%',
       }}
      >
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell style={{ borderBottom: '2px solid gray' }}>
                Emp .ID
              </TableCell>
              <TableCell style={{ borderBottom: '2px solid gray' }}>
                Full Name
              </TableCell>
              <TableCell style={{ borderBottom: '2px solid gray' }}>
                Position
              </TableCell>
              <TableCell style={{ borderBottom: '2px solid gray' }}>
                Join Date
              </TableCell>
              <TableCell style={{ borderBottom: '2px solid gray' }}>
                Action
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data?.map((row) => (
                <TableRow key={row._id}>
                  <TableCell
                    component="th"
                    scope="row"
                    style={{ borderBottom: 'none' }}
                  >
                    {row.employee}
                  </TableCell>
                  <TableCell
                    style={{ borderBottom: 'none' }}
                  >{`${row.firstname} ${row.lastname} `}</TableCell>
                  <TableCell style={{ borderBottom: 'none' }}>
                    {row.position}
                  </TableCell>
                  <TableCell style={{ borderBottom: 'none' }}>
                    {row?.joinDate ? new Intl.DateTimeFormat('en-GB', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date(row?.joinDate)) : 'N/A'}
                  </TableCell>

                  <TableCell style={{ borderBottom: 'none' }}>
                    <div style={{ display: 'flex' }}>
                      <IconButton
                        className={classes.button1}
                        style={{ paddingRight: '15px' }}
                        component={Link}
                        to={`/employee/${row._id}`}
                      >
                        <VisibilityIcon />
                      </IconButton>
                      <IconButton
                        className={classes.button1}
                        component={Link}
                        to={`/updateemployee/${row._id}`}
                      >
                        <LaunchIcon />
                      </IconButton>

                      <IconButton
                        className={classes.button2}
                        onClick={() => HandleDeleteOne(row._id)}
                      >
                        <DeleteIcon className={classes.delete} />
                      </IconButton>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            {emptyRows > 0 && (
              <TableRow style={{ height: 53 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  );
}

export default EmployeeTable;
