import React, { useCallback, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AllLeavetable from '../admin/leaveManagement/AllLeavetable';
import { Helmet } from 'react-helmet';
import { LEAVE_URL } from '../../apis/apiUrls';
import axios from 'axios';
import { useSelector } from 'react-redux';
import store from '../../store';
import { loadUser } from '../../actions/authAction/auth';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
}));

function ManagerLeave() {
  const auth = useSelector((state) => state.auth);
  const id = auth?.user?._id;
  const classes = useStyles();
  const [searchKeyword, setSearchKeyword] = useState('');
  const [data, setData] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [size, setSize] = useState(5);
  const [totalElements, setTotalElements] = useState(0);
  const getLeaves = useCallback(
    async (p, s) => {
      axios.get(`${LEAVE_URL}?manager=${id}&keyword=${searchKeyword}&page=${p}&size=${s}`).then((users) => {
          setData(users?.data?.content);
          setTotalElements(users?.data?.totalElements);
      });
    },
    [searchKeyword, id]
  );
  useEffect(() => {
    getLeaves(pageNumber, size);
  }, [pageNumber, size, getLeaves, id]);
  useEffect(() => {
    store.dispatch(loadUser());
  }, []);
  return (
    <>
      <Helmet>
        <title>Assigned Leave</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <div
          style={{
            minHeight: '59vh',
            width: '100%',
            borderRadius: '0px',
            background: '#F5F5F5',
            marginTop: '0.5%',
          }}
        >
          <AllLeavetable  
            setSize={setSize}
            setSearchKeyword={setSearchKeyword}
            searchKeyword={searchKeyword}
            setPageNumber={setPageNumber}
            data={data}
            totalElements={totalElements} />
          <br />
        </div>
      </div>
    </>
  );
}

export default ManagerLeave;
