import React, { useState, useEffect, useCallback } from 'react';
import LeavePolicyTable from '../../layout/LeavePolicyTable';
import { useDispatch } from 'react-redux';
import { deleteLeavePolicy } from '../../../actions/adminPolicyAction';
import { makeStyles } from '@material-ui/core/styles';
import { Helmet } from 'react-helmet';
import { LEAVE_POLICY_URL } from '../../../apis/apiUrls';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
    background: '#F5F5F5'
  },
}));

function ManageLeavePolicy() {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [searchKeyword, setSearchKeyword] = useState('');
  const [data, setData] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [size, setSize] = useState(5);
  const [totalElements, setTotalElements] = useState(0);
  const getLeaves = useCallback(
    async (p, s) => {
      axios.get(`${LEAVE_POLICY_URL}?keyword=${searchKeyword}&page=${p}&size=${s}`).then((users) => {
          setData(users?.data?.content);
          setTotalElements(users?.data?.totalElements);
      });
    },
    [searchKeyword]
  );
  useEffect(() => {
    getLeaves(pageNumber, size);
  }, [pageNumber, size, getLeaves, totalElements]);
  const HandleDeleteLeavePolicy = (id) => {
    dispatch(deleteLeavePolicy(id));
    setData((pre) => pre?.filter((item) => item._id !== id));
    setTotalElements((pre) => pre - 1);
  };
  return (
    <>
      <Helmet>
        <title>Manage Policy</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <div
          style={{
            minHeight: '59vh',
            width: '100%',
            borderRadius: '0px',
            background: '#F5F5F5',
            marginTop: '0.5%',
            // // boxShadow: '5.29353px 0px 13.2338px rgba(0, 0, 0, 0.2)',
          }}
        >
          <br />
          <LeavePolicyTable
            setSize={setSize}
            setSearchKeyword={setSearchKeyword}
            searchKeyword={searchKeyword}
            setPageNumber={setPageNumber}
            data={data}
            totalElements={totalElements}            
            HandleDeleteLeavePolicy={HandleDeleteLeavePolicy}
          />
        </div>
      </div>
    </>
  );
}

export default ManageLeavePolicy;
