import React, { useState } from 'react';
import Alert from '../../layout/Alert';
import { useDispatch, useSelector } from 'react-redux';
import { addLeavePolicy } from '../../../actions/adminPolicyAction';
import { makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { useNavigate } from 'react-router-dom';
import store from '../../../store';
import { remove } from '../../../actions/alert';
import { Helmet } from 'react-helmet';
import LoadingSpinner from '../../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      backgroundColor: '#0EA900',
    },
  },
}));

const AddLeavePolicy = ({ alerts }) => {
  const dispatch = useDispatch();
  const alert = useSelector((state) => state.alert);
  const classes = useStyles();
  const navigate = useNavigate();
  const [showLoader, setShowLoader] = useState(false)
  const formik = useFormik({
    initialValues: {
      title: '',
      content: '',
    },
    validationSchema: Yup.object({
      title: Yup.string().required('title is required'),
      content: Yup.string().required('content is required'),
    }),
    onSubmit: (values) => {
      setShowLoader(true)
      setTimeout(() => setShowLoader(false), 1000)
      let title = values.title;
      let content = values.content;
      dispatch(addLeavePolicy({ title, content }));
    },
  });
  if (alert[0]?.msg === 'successfully created') {
    store.dispatch(remove(alert[0]?.msg, alert[0]?.alertType, alert[0]?.id));
    navigate('/managePolicy');
  }

  return (
    <>
      <Helmet>
        <title>Add Policy</title>
      </Helmet>
      <div className={classes.mainContainer}>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
        <div
          style={{
            background: '#FFFFFF',
            boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
            borderRadius: '20px',
            width: '100%',
            marginTop: '0.5%',
            padding: '15px',
          }}
        >   
          <form onSubmit={formik.handleSubmit}>
          <Grid
            container
            xs={12}
            lg={6}
            spacing={3}
            style={{ marginLeft: '5%', marginTop: '2%', marginRight: 'auto' }}
          >
            <h3 className="title">Add Policy </h3>
          </Grid>
        
          <Grid
            container
            xs={12}
            spacing={3}
          >
            <Grid
                    container
                    spacing={3}
                    style={{ marginLeft: '5%', marginTop: '2%', marginRight: 'auto' }}
                  >
              <Grid item lg={6} xs={6} md={6} >
              <div>
                <label htmlFor="">Policy Title</label>
                <input
                  id="standard-basic"
                  placeholder="Enter a Policy Title"
                  label="Standard"
                  name="title"
                  className={
                    formik.touched.title &&
                    formik.errors.title
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.title}
                  fullWidth
                />
              </div>
              {formik.touched.title &&
              formik.errors.title ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.title}
                </div>
              ) : null}
              </Grid>
              <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Policy Content (Description)</label>
                <textarea
                  id="standard-basic"
                  placeholder="Enter a Policy Description"
                  label="Standard"
                  name="content"
                  className={
                    formik.touched.content &&
                    formik.errors.content
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.content}
                  fullWidth
                  style={{height: '200px'}}
                />
              </div>
              {formik.touched.content &&
              formik.errors.content ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.content}
                </div>
              ) : null}
              </Grid>
            </Grid>
              <Alert />

              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
                    <Button
                      variant="contained"
                      className={classes.btn}
                      color="secondary"
                      type="submit"
                      disabled={alert.length > 0 ? true : false}
                    >
                      Submit
                    </Button>
              
                  </Grid>

          </Grid>              
          </form>
          <br />
        </div>
      }
      </div>
    </>
  );
};

AddLeavePolicy.propTypes = {
  alerts: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  AddLeavePolicy: state.alert,
});

export default connect(mapStateToProps)(AddLeavePolicy);
