import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import { IconButton, ListItemIcon } from '@material-ui/core';
import CloseIcon from '@mui/icons-material/Close';
import { Divider, Grid } from '@mui/material';
import { NEW_NOTIFICATION_URL, OLD_NOTIFICATION_URL, VIEW_NOTIFICATION_URL } from '../../apis/apiUrls';
import { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { getNotificationCount } from '../../actions/leaveAction';
import { useDispatch } from 'react-redux';


export default function Notification(props) {
  const { onClose, open } = props;
  const [newNotifications, setNewNotifications] = useState([]);
  const [oldNotifications, setOldNotifications] = useState([]);
  const navigate = useNavigate();
  const [refresh, setRefresh] = useState(false);
  const dispatch = useDispatch();
  const handleClose = () => {
    onClose();
  };

  const handleListItemClick = async (value) => {
    await axios.patch(`${VIEW_NOTIFICATION_URL}/${value}`);
    setRefresh(!refresh);
    navigate(`/leavedetails/${value}`);
    onClose();
  };
  const getAllNotification = () => {
    axios.get(NEW_NOTIFICATION_URL).then((user) => {
      setNewNotifications(user.data);
    });
    axios.get(OLD_NOTIFICATION_URL).then((user) => {
      setOldNotifications(user.data);
    });
  };
  useEffect(() => {
    getAllNotification();
    dispatch(getNotificationCount());
  }, [refresh, dispatch]);
  // useEffect(() => {
  //   store.dispatch(getNotificationCount());
  // }, [refresh]);
  return (
    <Dialog onClose={handleClose} open={open} >
      <DialogTitle sx={{ m: 0, p: 2 }}>
      <div style={{display: 'flex', justifyContent: 'space-between'}}>
        <p style={{
                    fontFamily: "Roboto",
                    fontSize: '34px',
                    fontWeight: '700',
                    lineHeight: '39.84px'}}
        >Notifications
        
        </p>
        <IconButton
            aria-label="close"
            onClick={handleClose}
          >
            <CloseIcon />
        </IconButton>
      </div>
      </DialogTitle>
        <Divider/>
        { ( newNotifications.length === 0 && oldNotifications.length === 0 ) ? 
        <div style={{display: 'flex', justifyContent: 'center', paddingTop: '20%', paddingBottom: '20%'}}> 
          <DialogTitle className='title'>
            <p style={{
                        fontFamily: "Roboto",
                        fontSize: '26px',
                        fontWeight: '500',
                        lineHeight: '30.47px',
                      }}
            >You don't have any new notification</p>
        </DialogTitle>
        </div> : ''}
        {newNotifications.length > 0 ? <DialogTitle className='title'>
        <p style={{
                    fontFamily: "Roboto",
                    fontSize: '26px',
                    fontWeight: '500',
                    lineHeight: '30.47px',
                  }}
        >New</p>
        </DialogTitle> : ''
        }
        <List sx={{ pt: 0 }}>
        {newNotifications.map((notification) => (
          <ListItem disableGutters key={notification} style={{ paddingLeft: '8px', paddingRight: '8px'}}>
            <ListItemButton onClick={() => handleListItemClick(notification._id)}>
              <ListItemIcon>
                <img src="/ClockIcon.png" alt="" />
              </ListItemIcon>
              <ListItemText primary={
                <div>
                <Grid item lg={12} md={12} xs={12}>
                  <p style={{
                    fontFamily: "Roboto",
                    fontSize: '22px',
                    fontWeight: '500',
                    lineHeight: '25.78px'}}
                  > <b>{notification.name}</b> apply a {notification.leaveCategory} for {notification.noOfDays} day</p>
                </Grid>
                <Grid item lg={12} md={12} xs={12}>
                <Grid container>
                  <Grid item lg={6} md={6} xs={6}>
                  <p style={{
                    fontFamily: "Roboto",
                    fontSize: '18px',
                    fontWeight: '400',
                    lineHeight: '21.09px'}}
                  >Posting date {notification.date}</p>
                  </Grid>                  
                  <Grid item lg={6} md={6} xs={6} style={{justifyContent: 'right',display: 'flex',}}>
                      <div style={{
                        color:
                          notification.status === 'Pending'
                            ? '#FFA80C'
                            : notification.status === 'Granted'
                            ? '#06BD02'
                            : '#D90101',
                        background:
                        notification.status === 'Pending'
                            ? 'rgba(255, 168, 12, 0.2)'
                            : notification.status === 'Granted'
                            ? 'rgba(6, 189, 2, 0.2)'
                            : 'rgba(217, 1, 1, 0.2)',
                        boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
                        borderRadius: '51px',
                        padding: '4px, 2px, 4px, 2px',
                        gap: '10px',
                        alignItems: 'center',
                        justifyContent: 'center',
                        display: 'flex',
                        width: '90px',
                        position: 'relative',
                        height: '25px',
                      }}>
                      {notification.status}
                    </div>
                  </Grid>                  
                </Grid>                  
                </Grid>                  
                </div>
              } />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      {oldNotifications.length > 0 ? <DialogTitle className='title'>
        <p style={{
                    fontFamily: "Roboto",
                    fontSize: '26px',
                    fontWeight: '500',
                    lineHeight: '30.47px',
                  }}
        >Last 7 days</p>
        </DialogTitle> : ''
        }
      <List sx={{ pt: 0 }}>
        {oldNotifications.map((notification) => (
          <ListItem disableGutters key={notification} style={{ paddingLeft: '8px', paddingRight: '8px'}}>
            <ListItemButton onClick={() => handleListItemClick(notification._id)}>
              <ListItemIcon>
                <img src="/ClockIcon.png" alt="" />
              </ListItemIcon>
              <ListItemText primary={
                <div>
                <Grid item lg={12} md={12} xs={12}>
                  <p style={{
                    fontFamily: "Roboto",
                    fontSize: '22px',
                    fontWeight: '500',
                    lineHeight: '25.78px'}}
                  > <b>{notification.name}</b> apply a {notification.leaveCategory} for {notification.noOfDays} day</p>
                </Grid>
                <Grid item lg={12} md={12} xs={12}>
                <Grid container>
                  <Grid item lg={6} md={6} xs={6}>
                  <p style={{
                    fontFamily: "Roboto",
                    fontSize: '18px',
                    fontWeight: '400',
                    lineHeight: '21.09px'}}
                  >Posting date {notification.date}</p>
                  </Grid>                  
                  <Grid item lg={6} md={6} xs={6} style={{justifyContent: 'right',display: 'flex',}}>
                      <div style={{
                        color:
                          notification.status === 'Pending'
                            ? '#FFA80C'
                            : notification.status === 'Granted'
                            ? '#06BD02'
                            : '#D90101',
                        background:
                        notification.status === 'Pending'
                            ? 'rgba(255, 168, 12, 0.2)'
                            : notification.status === 'Granted'
                            ? 'rgba(6, 189, 2, 0.2)'
                            : 'rgba(217, 1, 1, 0.2)',
                        boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
                        borderRadius: '51px',
                        padding: '4px, 2px, 4px, 2px',
                        gap: '10px',
                        alignItems: 'center',
                        justifyContent: 'center',
                        display: 'flex',
                        width: '90px',
                        position: 'relative',
                        height: '25px',
                      }}>
                      {notification.status}
                    </div>
                  </Grid>                  
                </Grid>                  
                </Grid>                  
                </div>
              } />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
}