import React, { useState } from 'react';
import Alert from '../../layout/Alert';
import { useDispatch, useSelector } from 'react-redux';
import { addLeaveType } from '../../../actions/adminLeaveAction';
import { makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { useNavigate } from 'react-router-dom';
import store from '../../../store';
import { remove } from '../../../actions/alert';
import { Helmet } from 'react-helmet';
import LoadingSpinner from '../../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      backgroundColor: '#0EA900',
    },
  },
}));

const AddLeaveType = ({ alerts }) => {
  const dispatch = useDispatch();
  const alert = useSelector((state) => state.alert);
  const classes = useStyles();
  const navigate = useNavigate();
  const [showLoader, setShowLoader] = useState(false);
  const formik = useFormik({
    initialValues: {
      leaveType: '',
      numberLeave: '',
      carryOverPercentage: '',
      carryOverMaxDays: '',
      description: '',
    },
    validationSchema: Yup.object({
      leaveType: Yup.string().required('LeaveType is required'),
      numberLeave: Yup.string().required('Number is required'),
      carryOverMaxDays: Yup.number().required('carryOver Max days is required'),
      carryOverPercentage: Yup.number().required('carryOver Percentage is required')
        .min(0, 'Percentage must be greater than or equal to 0')
        .max(100, 'Percentage must be less than or equal to 100'),
    }),
    onSubmit: (values) => {
      setShowLoader(true)
      setTimeout(() => setShowLoader(false), 1000)
      let leaveType = values.leaveType;
      let numberLeave = values.numberLeave;
      let carryOverPercentage = values.carryOverPercentage;
      let carryOverMaxDays = values.carryOverMaxDays;
      let description = values.description;
      dispatch(addLeaveType({ leaveType, numberLeave, description, carryOverPercentage, carryOverMaxDays }));
    },
  });
  if (alert[0]?.msg === 'successfully created') {
    store.dispatch(remove(alert[0]?.msg, alert[0]?.alertType, alert[0]?.id));
    navigate('/updateleavetype');
  }

  return (
    <>
      <Helmet>
        <title>Add Leave Type</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <div
          style={{
            background: '#FFFFFF',
            boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
            borderRadius: '20px',
            width: '100%',
            marginTop: '0.5%',
            padding: '15px',
          }}
        >   
          <form onSubmit={formik.handleSubmit}>
          <Grid
            container
            xs={12}
            lg={6}
            spacing={3}
            style={{ marginLeft: '5%', marginTop: '2%', marginRight: 'auto' }}
          >
            <h3 className="title">Add Leave Type </h3>
          </Grid>
        
          <Grid
            container
            xs={12}
            spacing={3}
          >
            <Grid
                    container
                    spacing={3}
                    style={{ marginLeft: '5%', marginTop: '2%', marginRight: 'auto' }}
                  >
              <Grid item lg={6} xs={12} md={6} >
                <label htmlFor="">Leave Type</label>
                <input
                  type="text"
                  placeholder="Add Leave Type"
                  name="leaveType"
                  className={
                    formik.touched.leaveType && formik.errors.leaveType
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.leaveType}
                />
              {formik.touched.leaveType && formik.errors.leaveType ? (
                <div style={{ color: 'red' }}>{formik.errors.leaveType}</div>
              ) : null}               
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Leave Allowed</label>
                <input
                  type="number"
                  placeholder="Enter allowed leaves"
                  name="numberLeave"
                  className={
                    formik.touched.numberLeave && formik.errors.numberLeave
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.numberLeave}
                />
                {formik.touched.numberLeave && formik.errors.numberLeave ? (
                  <div style={{ color: 'red' }}>
                    {formik.errors.numberLeave}
                  </div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Carry Over Percentage Value</label>
                <input
                  type="number"
                  placeholder="Enter next year carry over percentage"
                  name="carryOverPercentage"
                  className={
                    formik.touched.carryOverPercentage && formik.errors.carryOverPercentage
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.carryOverPercentage}
                />
                {formik.touched.carryOverPercentage && formik.errors.carryOverPercentage ? (
                  <div style={{ color: 'red' }}>
                    {formik.errors.carryOverPercentage}
                  </div>
                ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
                <label htmlFor="">Carry Over Maximum Days</label>
                <input
                  type="number"
                  placeholder="Enter next year carry over maximum days"
                  name="carryOverMaxDays"
                  className={
                    formik.touched.carryOverMaxDays && formik.errors.carryOverMaxDays
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.carryOverMaxDays}
                />
                {formik.touched.carryOverMaxDays && formik.errors.carryOverMaxDays ? (
                  <div style={{ color: 'red' }}>
                    {formik.errors.carryOverMaxDays}
                  </div>
                ) : null}
              </Grid>
              <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Description</label>
                <textarea
                  id="standard-basic"
                  placeholder="Enter a Description"
                  label="Standard"
                  name="description"
                  className={
                    formik.touched.description &&
                    formik.errors.description
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.description}
                  fullWidth
                  style={{height: '100px'}}
                />
              </div>
              {formik.touched.description &&
              formik.errors.description ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.description}
                </div>
              ) : null}
              </Grid>
            </Grid>
              <Alert />

              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
                    <Button
                      variant="contained"
                      className={classes.btn}
                      color="secondary"
                      type="submit"
                      disabled={alert.length > 0 ? true : false}
                    >
                      {showLoader === true ? <LoadingSpinner></LoadingSpinner> : 'Submit'}                      
                    </Button>
              
                  </Grid>

          </Grid>              
          </form>
          <br />
        </div>
      </div>
    </>
  );
};

AddLeaveType.propTypes = {
  alerts: PropTypes.array.isRequired,
};

const mapStateToProps = (state) => ({
  AddLeaveType: state.alert,
});

export default connect(mapStateToProps)(AddLeaveType);
