import React, { useEffect, useState } from 'react';
import Alert from '../layout/Alert';
import { addLeave, clearState } from '../../actions/leaveAction';
import { useDispatch, useSelector } from 'react-redux';
import { Checkbox, FormControlLabel, MenuItem, Select, makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import store from '../../store';
import { getLeaveType } from '../../actions/adminLeaveAction';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import LoadingSpinner from '../LoadingSpinner';
import { loadUser } from '../../actions/authAction/auth';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      //you want this to be the same as the backgroundColor above
      backgroundColor: '#0EA900',
    },
  },
  inputstyle: {
    background: '#ffffff',
    borderRadius: '10px 10px 0px 0px !important',
    height: '50px',
    padding: '14px',
    borderBottom: '2px solid #000 !important',
    border: 'none !important',
  },
  checkboxstyle: {
    background: '#ffffff',
    borderRadius: '10px 10px 0px 0px !important',
    height: '50px',
    padding: '14px',
    borderBottom: '2px solid #000 !important',
    border: 'none !important',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));
const AddLeave = () => {
  const adminleave = useSelector((state) => state.adminleave);
  const auth = useSelector((state) => state.auth);
  const leave = useSelector((state) => state.leave);
  const alert = useSelector((state) => state.alert);
  const dispatch = useDispatch();
  const classes = useStyles();
  const navigate = useNavigate();
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [checked, setChecked] = React.useState(false); 
  const [showLoader, setShowLoader] = useState(false)
  const [noOfDay, setNoOfDay] = useState(1)
  useEffect(() => {
    const currentDate = new Date();
    currentDate.setDate(currentDate.getDate() + 1);
    const year = currentDate.getFullYear();
    const month = (currentDate.getMonth() + 1).toString().padStart(2, '0');
    const day = currentDate.getDate().toString().padStart(2, '0');
    const tomorrowDate = `${year}-${month}-${day}`;
    setStartDate(tomorrowDate);
    setEndDate(tomorrowDate);
  }, []);
  const handleStartDateChange = (event) => {
    setStartDate(event.target.value);
    changedNoOfDays(event.target.value, endDate);
  };
  const handleEndDateChange = (event) => {
    setEndDate(event.target.value);
    changedNoOfDays(startDate, event.target.value);
  };
  function handleHalfDayChange(e) {
    setChecked(e.target.checked);
    if(checked){
      setEndDate(startDate);
      changedNoOfDays(startDate, endDate);
    }else{
      setNoOfDay(0.5);
    }
  }
  const handleDateChange = (event) => {
    setStartDate(event.target.value);
    setEndDate(event.target.value);
    setNoOfDay(0.5);
  };
  const changedNoOfDays = (sDate, eDate) => {
    const start = new Date(sDate);
    const end = new Date(eDate);
    const days = ((end - start) / (1000 * 60 * 60 * 24)) + 1;
    setNoOfDay(days);
  }
  
  useEffect(() => {
    store.dispatch(loadUser());
  }, []);

  const formik = useFormik({
    initialValues: {
      leaveCategory: '',
      leaveDescription: '',
    },
    validationSchema: Yup.object({
      leaveCategory: Yup.string().required('Leave category is required'),
      leaveDescription: Yup.string().required('Leave description is required'),
    }),
    onSubmit: (values, { resetForm }) => {
      setShowLoader(true)
      setTimeout(() => setShowLoader(false), 1000)
      let leaveCategory = values.leaveCategory;
      let leaveDescription = values.leaveDescription;
      let date = new Intl.DateTimeFormat('en-US', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date());
      let formatStartDate = new Intl.DateTimeFormat('en-US', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date(startDate));
      let formatEndDate = new Intl.DateTimeFormat('en-US', { day: '2-digit', month: '2-digit', year: 'numeric' }).format(new Date(endDate));
      dispatch(
        addLeave({
          startDate: formatStartDate,
          endDate: formatEndDate,
          halfDay: checked,
          leaveCategory,
          leaveDescription,
          userid: auth?.user?._id,
          name: `${auth?.user?.firstname} ${auth.user.lastname}`,
          manager: auth?.user?.manager,
          employee: auth?.user?.employee,
          gender: auth?.user?.gender,
          username: auth?.user?.username,
          phoneNo: auth?.user?.phoneNo,
          joinDate: auth?.user?.joinDate,
          adminRemark: '',
          date: date,
        })
      );
    },
  });
  if (leave?.data?.error === null) {
    store.dispatch(clearState());
    navigate('/userleave');
  }
  useEffect(() => {
    store.dispatch(getLeaveType());
  }, []);

  return (
    <>
      <Helmet>
        <title>Apply Leave</title>
      </Helmet>
      <div className={classes.mainContainer}>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
        <div
          style={{
            background: '#FFFFFF',
            boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
            borderRadius: '20px',
            width: '100%',
            marginTop: '0.5%',
            padding: '15px',
          }}
        >
            <form noValidate autoComplete="off" onSubmit={formik.handleSubmit}>
            <Grid
              container
              xs={12}
              lg={6}
              spacing={2}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <h3 className="title">Apply Leave </h3>
            </Grid>
            <Grid
              container
              xs={12}
              spacing={3}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <Grid item lg={6} xs={12} md={6} >
              <div>
                <label htmlFor="">Leave Type</label>
                <Select
                        name="leaveCategory"
                        className={
                          formik.touched.leaveCategory && formik.errors.leaveCategory
                            ? 'inputstyleTwo'
                            : 'inputstyle'
                        }
                        onChange={formik.handleChange}
                        value={formik.values.leaveCategory}
                      >
                        {adminleave?.map((n) => {
                            return (
                              <MenuItem 
                              key={n._id}
                              value={n.leaveType}
                              selected={formik.values.leaveCategory}>
                                <div
                                  style={{
                                    display: 'flex',
                                    alignItems: 'center',
                                  }}
                                >
                                  <span>
                                    {n.leaveType}
                                  </span>
                                </div>
                              </MenuItem>
                            );
                        })}
                </Select>
              </div>
              {formik.touched.leaveCategory && formik.errors.leaveCategory ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.leaveCategory}
                </div>
              ) : null}
              </Grid>              
              <Grid item lg={6} xs={12} md={6}>
              <FormControlLabel 
              control={
              <Checkbox 
                checked={checked}
                onChange={handleHalfDayChange}
              />
              } 
              label="Half Day" 
              style={{marginTop: '6%'}}
              />
              </Grid>
              { checked === true ? 
              <Grid item lg={6} xs={12} md={6} >
              <div>
                <label htmlFor="">Leave Date</label>
                <input
                  id="standard-basic"
                  name="leaveDate"
                  // label="Standard"
                  type='date'
                  fullWidth
                  onChange={handleDateChange}
                  className={
                    formik.touched.leaveDate && formik.errors.leaveDate
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  defaultValue={startDate}
                  value={startDate}
                />
              </div>
              </Grid>
              :
              <>
              <Grid item lg={6} xs={12} md={6} >
              <div>
                <label htmlFor="">Start Date</label>
                <input
                  id="standard-basic"
                  name="startDate"
                  // label="Standard"
                  type='date'
                  fullWidth
                  onChange={handleStartDateChange}
                  className={
                    formik.touched.startDate && formik.errors.startDate
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  defaultValue={startDate}
                  value={startDate}
                />
              </div>
              </Grid>
              <Grid item lg={6} xs={12} md={6} >
              <div>
                <label htmlFor="">End Date</label>
                <input
                  id="standard-basic"
                  name="endDate"
                  label="Standard"
                  type='date'
                  fullWidth
                  onChange={handleEndDateChange}
                  className={
                    formik.touched.endDate && formik.errors.endDate
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  defaultValue={endDate}
                  value={endDate}
                />
              </div>
              </Grid>
              </>
              }
              <Grid item lg={6} xs={12} md={6} >
              <div>
                <label htmlFor="">Number Of Days</label>
                <input
                  id="standard-basic"
                  type='text'
                  className={'inputstyle'}
                  fullWidth
                  defaultValue={noOfDay}
                  value={noOfDay}
                  disabled
                  style={{color: '#9E9E9E'}}
                />
              </div>
              </Grid>
              <br /> 
              <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Description</label>
                <textarea
                  id="standard-basic"
                  placeholder="Enter a Description"
                  label="Standard"
                  name="leaveDescription"
                  className={
                    formik.touched.leaveDescription &&
                    formik.errors.leaveDescription
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.leaveDescription}
                  fullWidth
                  style={{height: '100px'}}
                />
              </div>
              {formik.touched.leaveDescription &&
              formik.errors.leaveDescription ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.leaveDescription}
                </div>
              ) : null}
              </Grid>
              <Alert />
              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
              <Button
                variant="contained"
                className={classes.btn}
                color="secondary"
                type="submit"
                disabled={alert.length > 0 ? true : false}
              > Apply
              </Button>
              </Grid>
            </Grid>
            </form>
        </div>
      }
      </div>
    </>
  );
};

export default AddLeave;
