import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Toolbar from '@material-ui/core/Toolbar';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { logout } from '../../actions/authAction/auth';
import { connect } from 'react-redux';
import { loadUser } from '../../actions/authAction/auth';
import MenuIcon from '@mui/icons-material/Menu';
import PermIdentityIcon from '@mui/icons-material/PermIdentity';
import SpeakerNotesIcon from '@mui/icons-material/SpeakerNotes';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import CodeIcon from '@mui/icons-material/Code';
import PersonIcon from '@mui/icons-material/Person';
import NotificationsIcon from '@mui/icons-material/Notifications';
import { useNavigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';

import store from '../../store';
import './Drawer.css';
import { getNotificationCount } from '../../actions/leaveAction';
import axios from 'axios';
import config from '../../config';
import Notification from '../notification/Notification';
import { Badge } from '@material-ui/core';
import { getLeavePolicies } from '../../actions/adminPolicyAction';
const drawerWidth = 420;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    background: '#F5F5F5',
  },
  page: {
    background: '#F5F5F5',
    width: '100%',
    height: '100%',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    },
    [theme.breakpoints.down('md')]: {
      display: 'none',
    },
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
    [theme.breakpoints.down('xs')]: {
      display: 'block',
      marginBottom: '50px',
      background: '#490F4C',
      opacity: '80%',
    },
  },
  active: {
    background: 'rgba(255, 231, 234, 0.2) !important',
    marginLeft: 'auto',
    marginRight: 'auto',
    borderRadius: '52px',
    width: '80%',
    '&:hover': {
      backgroundColor: 'rgba(255, 231, 234, 0.2)  !important', // Change this to the desired hover color
    },
    marginBottom: '10px',
    marginTop: '10px',
  },
  active2: {
    marginLeft: 'auto',
    marginRight: 'auto',
    borderRadius: '52px',
    width: '80%',
    '&:hover': {
      backgroundColor: 'rgba(255, 231, 234, 0.2)  !important', // Change this to the desired hover color
    },
    // marginBottom: '10px'
  },
  menuButton: {
    marginRight: theme.spacing(2),
    color: '#fff',
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    boxShadow: 'rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px',
    background:
      'linear-gradient(115.53deg, #39103C 1.96%, #A307AB 103.09%) !important',
    color: '#fff !important',
    overflowY: 'auto', // Enable vertical scrolling
    '&::-webkit-scrollbar': {
      width: '1px !important',
    },
    '&::-webkit-scrollbar-thumb': {
      backgroundColor: '#00000000',
    },
    '&::-webkit-scrollbar-track': {
      backgroundColor: 'transparent',
    },
    scrollbarWidth: '1px',
    scrollbarColor: '#00000000',
  },
  listItemText: {
    fontFamily: 'Roboto',
    fontSize: '24px',
    fontWeight: '400',
    letterSpacing: '10%',
    lineHeight: '28.13px',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));
const dashboard = [
  {
    text: 'Dashboard',
    icon: <img src="/dashboard.png" alt="" width="24px" height="24px" />,
    path: '/admindashboard',
  },
];
const policyManagement = [
  {
    text: 'Add Policy',
    path: '/addPolicy',
  },
  {
    text: 'Manage Policy',
    path: '/managePolicy',
  },
];
const menuItems = [
  {
    text: 'Change Password',
    icon: <img src="/password.svg" alt="" />,
    path: '/changepassword',
  },
];
const profile = [
  {
    text: 'My Profile',
    icon: <PermIdentityIcon style={{ color: '#ffffff' }} />,
    path: '/myprofile',
  },
];

const levaemenuItems = [
  {
    text: 'Apply Leave',
    path: '/addleave',
  },
  {
    text: 'My Leaves',
    path: '/userleave',
  },
];
const Employees = [
  {
    text: 'Add Employee',
    path: '/register',
  },
  {
    text: 'Manage Employee',
    path: '/employee',
  },
];
const ManagerEmployees = [
  {
    text: 'Manage Employee',
    path: '/employee',
  },
];
const leaveManagement = [
  {
    text: 'All Leaves',
    path: '/allleave',
  },
  {
    text: 'Pending Leave',
    path: '/pendingleave',
  },
  {
    text: 'Granted Leave',
    path: '/grantedleave',
  },
  {
    text: 'Rejected Leave',
    path: '/rejectedleave',
  },
];
const leaveType = [
  {
    text: 'Add Leave Type',
    path: '/addleavetype',
  },
  {
    text: 'Manage Leave type',
    path: '/updateleavetype',
  },
];

function DrawerBar({ children, window, logout, auth: { user } }) {
  const classes = useStyles();
  const theme = useTheme();
  const location = useLocation();
  const navigate = useNavigate();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [check, setCheck] = useState(false);
  const [checknotification, setCheckNotification] = useState(true);
  const [empcheck, setEmpCheck] = useState(false);
  const [leaveCheck, setLeaveCheck] = useState(false);
  const [levaeManagementCheck, setLevaeManagementCheck] = useState(false);
  const [policyManagementCheck, setPolicyManagementCheck] = useState(false);
  const [policiesCheck, setPoliciesCheck] = useState(false);
  const auth = useSelector((state) => state?.auth);
  const notificationCount = useSelector((state) => state?.notificationCount);
  const policies = useSelector((state) => state.adminPolicy);
  const id = auth?.user?._id;
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (value) => {
    setOpen(false);
  };

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getNotificationCount());
  }, [dispatch]);

  const notification = [
    {
      text: 'Notification',
      icon: (
        <Badge badgeContent={notificationCount} color="primary">
          <NotificationsIcon style={{ color: '#ffffff' }} />,
        </Badge>
      ),
    },
  ];

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  useEffect(() => {
    store.dispatch(loadUser());
    store.dispatch(getLeavePolicies());
  }, []);
  useEffect(() => {
    switch (location.pathname) {
      case '/employee':
        return (
          setEmpCheck(true),
          setLeaveCheck(false),
          setLevaeManagementCheck(false),
          setPolicyManagementCheck(false),
          setPoliciesCheck(false)
        );
      case '/register':
        return (
          setEmpCheck(true),
          setLeaveCheck(false),
          setLevaeManagementCheck(false),
          setPolicyManagementCheck(false),
          setPoliciesCheck(false)
        );
      case '/updateleavetype':
        return (
          setLeaveCheck(true),
          setEmpCheck(false),
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(false)
        );
      case '/addleavetype':
        return (
          setLeaveCheck(true),
          setEmpCheck(false),
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(false)
        );
      case '/allleave':
        return (
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(true),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      case '/pendingleave':
        return (
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(true),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      case '/grantedleave':
        return (
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(true),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      case '/rejectedleave':
        return (
          setPolicyManagementCheck(false),
          setPoliciesCheck(false),
          setLevaeManagementCheck(true),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      case '/managePolicy':
        return (
          setPolicyManagementCheck(true),
          setPoliciesCheck(false),
          setLevaeManagementCheck(false),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      case '/addPolicy':
        return (
          setPolicyManagementCheck(true),
          setPoliciesCheck(false),
          setLevaeManagementCheck(false),
          setEmpCheck(false),
          setLeaveCheck(false)
        );
      default:
        return location.pathname;
    }
  }, [location.pathname]);
  const seenNotification = () => {
    if (user?.role === 'admin') {
      axios.patch(`${config.apiUrl}users/notification/request`);
      setCheckNotification(!checknotification);
    }
    if (user?.role === 'manager') {
      axios.patch(`${config.apiUrl}users/notification/request/${id}`);
      setCheckNotification(!checknotification);
    }
  };
  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          padding: 0,
        }}
      >
        <img
          onClick={() => navigate('/')}
          src="/logo.svg"
          alt=""
          style={{ width: '150px', height: '150px' }}
        />
      </div>
      <br />
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          padding: 0,
          fontFamily: 'Roboto',
          fontSize: '30px',
          fontWeight: '400',
          letterSpacing: '10%',
          lineHeight: '35.16px',
          marginBottom: '3%',
        }}
      >
        {user?.firstname} {user?.lastname}
      </div>
      <div
        style={{
          width: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          fontFamily: 'Roboto',
          fontSize: '18px',
          fontWeight: '400',
          letterSpacing: '10%',
          lineHeight: '21.09px',
          marginBottom: '3%',
        }}
      >
        {user?.position}
      </div>
      <div
        style={{
          border: '0.7px solid #FFFFFF',
          width: '100%',
          height: '0px',
          marginLeft: 'auto',
          marginRight: 'auto',
          marginBottom: '5%',
        }}
      ></div>
      <Divider />
      <List>
        {user?.role === 'admin' ? (
          <>
            {dashboard.map((item) => (
              <>
                <ListItem
                  button
                  key={item.text}
                  onClick={() => {
                    navigate(item.path);
                    setEmpCheck(false);
                    setLevaeManagementCheck(false);
                    setPolicyManagementCheck(false);
                    setPoliciesCheck(false);
                    setLeaveCheck(false);
                    setMobileOpen(false);
                  }}
                  className={
                    location.pathname === item.path
                      ? classes.active
                      : classes.active2
                  }
                >
                  <ListItemIcon>{item.icon}</ListItemIcon>
                  <ListItemText
                    primary={item.text}
                    className={classes.listItemText}
                  />
                </ListItem>
              </>
            ))}
          </>
        ) : null}
        {user?.role === 'user' || user?.role === 'manager' ? (
          <>
            {profile.map((item) => (
              <>
                <ListItem
                  button
                  key={item.text}
                  onClick={() => {
                    navigate(item.path);
                    setMobileOpen(false);
                  }}
                  className={
                    location.pathname === item.path
                      ? classes.active
                      : classes.active2
                  }
                >
                  <ListItemIcon>{item.icon}</ListItemIcon>
                  <ListItemText
                    className={classes.listItemText}
                    primary={item.text}
                  />
                </ListItem>
              </>
            ))}{' '}
          </>
        ) : null}
        {user?.role === 'admin' || user?.role === 'manager' ? (
          <>
            {notification.map((item) => (
              <>
                <ListItem
                  button
                  key={item.text}
                  onClick={() => {
                    seenNotification();
                    handleClickOpen();
                    setMobileOpen(false);
                  }}
                  className={
                    location.pathname === item.path
                      ? classes.active
                      : classes.active2
                  }
                >
                  <ListItemIcon>{item.icon}</ListItemIcon>
                  <ListItemText
                    className={classes.listItemText}
                    primary={item.text}
                  />
                </ListItem>
                <Notification open={open} onClose={handleClose} />
              </>
            ))}{' '}
          </>
        ) : null}
        {user?.role === 'user' || user?.role === 'manager' ? (
          <>
            <ListItem
              button
              onClick={() => setCheck((prevCheck) => !prevCheck)}
              className={classes.active2}
            >
              <ListItemIcon>
                <SpeakerNotesIcon style={{ color: '#ffffff' }} />
              </ListItemIcon>
              <ListItemText className={classes.listItemText} primary="Leave" />
              {check ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {check ? (
              <>
                {levaemenuItems.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        style={{ fontSize: '24px' }}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
                {user?.role === 'manager' ? (
                  <>
                    <ListItem
                      button
                      key="Manager Leaves"
                      onClick={() => {
                        navigate('/managerleave');
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === '/managerleave'
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary="Assigned Leave"
                      />
                    </ListItem>
                  </>
                ) : null}
              </>
            ) : null}
          </>
        ) : null}
        {user?.role === 'admin' ? (
          <>
            <ListItem
              button
              onClick={() => {
                setLevaeManagementCheck((prevCheck) => !prevCheck);
                setPolicyManagementCheck(false);
                setPoliciesCheck(false);
                setEmpCheck(false);
                setLeaveCheck(false);
              }}
              className={classes.active2}
            >
              <ListItemIcon>
                <img src="/leaves.png" alt="" />
              </ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary="Leave Management"
              />
              {levaeManagementCheck ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {levaeManagementCheck ? (
              <>
                {leaveManagement?.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        ) : null}
        {user?.role === 'admin' ? (
          <>
            <ListItem
              button
              onClick={() => {
                setEmpCheck((prevCheck) => !prevCheck);
                setLevaeManagementCheck(false);
                setPolicyManagementCheck(false);
                setPoliciesCheck(false);
                setLeaveCheck(false);
              }}
              className={classes.active2}
            >
              <ListItemIcon>
                <PersonIcon style={{ color: '#ffffff' }} />
              </ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary="Employee's"
              />
              {empcheck ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {empcheck ? (
              <>
                {Employees.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        ) : null}
        {user?.role === 'manager' ? (
          <>
            <ListItem
              button
              onClick={() => {
                setEmpCheck((prevCheck) => !prevCheck);
                setLevaeManagementCheck(false);
                setPolicyManagementCheck(false);
                setPoliciesCheck(false);
                setLeaveCheck(false);
              }}
              className={classes.active2}
            >
              <ListItemIcon>
                <PersonIcon style={{ color: '#ffffff' }} />
              </ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary="Employee's"
              />
              {empcheck ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {empcheck ? (
              <>
                {ManagerEmployees.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        ) : null}
        {user?.role === 'admin' ? (
          <>
            <ListItem
              button
              onClick={() => {
                setLeaveCheck((prevCheck) => !prevCheck);
                setEmpCheck(false);
                setLevaeManagementCheck(false);
                setPolicyManagementCheck(false);
                setPoliciesCheck(false);
              }}
              className={classes.active2}
            >
              <ListItemIcon>
                <CodeIcon style={{ color: '#ffffff' }} />
              </ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary="Leave Type"
              />
              {leaveCheck ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {leaveCheck ? (
              <>
                {leaveType.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        ) : null}
        {user?.role === 'admin' ? (
          <>
            <ListItem
              button
              onClick={() => {
                setPolicyManagementCheck((prevCheck) => !prevCheck);
                setLeaveCheck(false);
                setEmpCheck(false);
                setLevaeManagementCheck(false);
                setPoliciesCheck(false);
              }}
              className={classes.active2}
            >
              <ListItemIcon>
                <img
                  src="/policy-management.svg"
                  alt=""
                  width="24px"
                  height="24px"
                />
                ,
              </ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary="Policy Management"
              />
              {policyManagementCheck ? (
                <ArrowDropDownIcon />
              ) : (
                <KeyboardArrowRightIcon fontSize="small" />
              )}
            </ListItem>

            {policyManagementCheck ? (
              <>
                {policyManagement.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.text}
                      onClick={() => {
                        navigate(item.path);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === item.path
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.text}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        ) : null}
        <>
          <ListItem
            button
            onClick={() => {
              setPoliciesCheck((prevCheck) => !prevCheck);
              setLeaveCheck(false);
              setEmpCheck(false);
              setLevaeManagementCheck(false);
              setPolicyManagementCheck(false);
            }}
            className={classes.active2}
          >
            <ListItemIcon>
              <img src="/rules.svg" alt="" width="24px" height="24px" />,
            </ListItemIcon>
            <ListItemText
              className={classes.listItemText}
              primary="Company Policies"
            />
            {policyManagementCheck ? (
              <ArrowDropDownIcon />
            ) : (
              <KeyboardArrowRightIcon fontSize="small" />
            )}
          </ListItem>
          <>
            {policiesCheck ? (
              <>
                {policies.map((item) => (
                  <>
                    <ListItem
                      button
                      key={item.title}
                      onClick={() => {
                        navigate(`/leavePolicy/${item._id}`);
                        setMobileOpen(false);
                      }}
                      className={
                        location.pathname === `/leavePolicy/${item._id}`
                          ? classes.active
                          : classes.active2
                      }
                    >
                      <ListItemIcon></ListItemIcon>
                      <ListItemText
                        className={classes.listItemText}
                        primary={item.title}
                      />
                    </ListItem>
                  </>
                ))}
              </>
            ) : null}
          </>
        </>
        {menuItems.map((item) => (
          <>
            <ListItem
              button
              key={item.text}
              onClick={() => {
                navigate(item.path);
                setEmpCheck(false);
                setLevaeManagementCheck(false);
                setPolicyManagementCheck(false);
                setPoliciesCheck(false);
                setLeaveCheck(false);
                setMobileOpen(false);
              }}
              className={
                location.pathname === item.path
                  ? classes.active
                  : classes.active2
              }
            >
              <ListItemIcon>{item.icon}</ListItemIcon>
              <ListItemText
                className={classes.listItemText}
                primary={item.text}
              />
            </ListItem>
          </>
        ))}{' '}
        <ListItem button onClick={() => logout()} className={classes.active2}>
          <ListItemIcon>
            <ExitToAppIcon style={{ color: '#ffffff' }} />
          </ListItemIcon>
          <ListItemText className={classes.listItemText} primary="Sign out" />
        </ListItem>
      </List>
    </div>
  );

  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>

      <div className={classes.page}>{children}</div>
    </div>
  );
}

DrawerBar.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

DrawerBar.propTypes = {
  logout: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { logout })(DrawerBar);
