import React, { useState, useEffect } from 'react';
import Alert from '../layout/Alert';
import { useDispatch } from 'react-redux';
import { updateLeaveType } from '../../actions/adminLeaveAction';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { LEAVE_TYPE_URL } from '../../apis/apiUrls';
import LoadingSpinner from '../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
    background: '#F5F5F5'
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      //you want this to be the same as the backgroundColor above
      backgroundColor: '#0EA900',
    },
  },
  background: '#F5F5F5'
}));
const UpdatePage = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [showLoader, setShowLoader] = useState(false)
  const [formData, setFormData] = useState({
    leaveType: '',
    numberLeave: '',
    carryOverPercentage: '',
    carryOverMaxDays: '',
    description: '',
  });
  const navigate = useNavigate();
  const { id } = useParams();
  const { leaveType, numberLeave, carryOverPercentage, carryOverMaxDays, description } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async (e) => {
    setShowLoader(true)
    setTimeout(() => setShowLoader(false), 1000)
    e.preventDefault();
    dispatch(updateLeaveType({ leaveType, numberLeave, description, carryOverPercentage, carryOverMaxDays, _id: id }));
    navigate('/updateleavetype');
  };
  const getLeaveById = async (id) => {
    await axios.get(`${LEAVE_TYPE_URL}/${id}`).then((res) => {
      setFormData(res.data);
    });
  };
  useEffect(() => {
    getLeaveById(id);
  }, [id]);

  return (
    <>
      <div className={classes.mainContainer}>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
        <div
          style={{
            background: '#FFFFFF',
            boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
            borderRadius: '20px',
            width: '100%',
            marginTop: '0.5%',
            padding: '15px',
          }}
        >
         <Grid
              container
              xs={12}
              lg={6}
              spacing={2}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <h3 className="title">Update Leave Type</h3>
            </Grid>
            <Grid
              container
              xs={12}
              spacing={3}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <Grid item lg={6} xs={12} md={6} >
              <label htmlFor="">Leave Type</label>
            <input
              type="text"
              className="inputstyle"
              placeholder="Add Leave Type"
              name="leaveType"
              value={leaveType}
              onChange={(e) => onChange(e)}
              required
            />
            </Grid>
            <Grid item lg={6} xs={12} md={6} >
            <label htmlFor="">Leave Allowed</label>
            <input
              type="number"
              className="inputstyle"
              name="numberLeave"
              value={numberLeave}
              onChange={(e) => onChange(e)}
              required
            />
            </Grid>
            <Grid item lg={6} xs={12} md={6} >
            <label htmlFor="">Carry Over Percentage Value</label>
            <input
              type="number"
              className="inputstyle"
              name="carryOverPercentage"
              value={carryOverPercentage}
              onChange={(e) => onChange(e)}
              required
            />
            </Grid>
            <Grid item lg={6} xs={12} md={6} >
            <label htmlFor="">Carry Over Maximum Days</label>
            <input
              type="number"
              className="inputstyle"
              name="carryOverMaxDays"
              value={carryOverMaxDays}
              onChange={(e) => onChange(e)}
              required
            />
            </Grid>
            <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Description</label>
                <textarea
                  id="standard-basic"
                  placeholder="Enter a Description"
                  label="Standard"
                  name="description"
                  className="inputstyle"
                  onChange={(e) => onChange(e)}
                  value={description}
                  fullWidth
                  style={{height: '100px'}}
                />
              </div>
              </Grid>
            <Alert />
              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
            <Button
              variant="contained"
              className={classes.btn}
              color="secondary"
              onClick={onSubmit}
            >
              update
            </Button>
          </Grid>
          </Grid>
        </div>
      }
      </div>
    </>
  );
};
export default UpdatePage;
