import axios from "axios";
import { LEAVE_POLICY_URL } from "../apis/apiUrls";
import { ADD_LEAVE_POLICY, DELETE_LEAVE_POLICY, GET_LEAVE_POLICY, UPDATE_LEAVE_POLICY } from "./adminLeavePolicy";
import { setAlert } from "./alert";

export const getLeavePolicies = () =>
    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };
        try {
            const res = await axios.get(
                LEAVE_POLICY_URL,
                config
            );
            dispatch({
                type: GET_LEAVE_POLICY,
                payload: res.data.content,
            });

        } catch (err) {
            const errors = err.response.data.errors;

            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }
        }
    }
export const addLeavePolicy = ({ title, content }) =>

    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };
        const body = JSON.stringify({ title, content });
        try {
            const res = await axios.post(
                LEAVE_POLICY_URL,
                body,
                config
            )
            if (res?.status === 201) {
                dispatch(setAlert("successfully created", "success"))
            }
            dispatch({
                type: ADD_LEAVE_POLICY,
                payload: res.data,
            });

        } catch (err) {
            const errors = err?.response.data.errors;

            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }
        }
    };
export const updateLeavePolicy = ({ title, content, _id }) =>
    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };
        const body = JSON.stringify({ title, content, _id });

        try {
            const res = await axios.patch(
                `${LEAVE_POLICY_URL}/${_id}`,
                body,
                config
            );

            dispatch({
                type: UPDATE_LEAVE_POLICY,
                payload: res.data,
            });

        } catch (err) {
            const errors = err.response.data.errors;

            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }
        }
    }
export const deleteLeavePolicy = (id) =>
    async (dispatch) => {
        const config = {
            headers: {
                "Content-Type": "application/json",
            },
        };
        try {
            await axios.delete(
                `${LEAVE_POLICY_URL}/${id}`,
                config,
                id
            );
            dispatch({
                type: DELETE_LEAVE_POLICY,
                payload: id,
            });

        } catch (err) {
            const errors = err.response.data.errors;
            if (errors) {
                errors.forEach((error) => dispatch(setAlert(error.msg, "danger")));
            }
        }
    }