export const addLeave =
  ({
    name,
    startDate,
    endDate,
    halfDay,
    leaveCategory,
    leaveDescription,
    userid,
    manager,
    employee,
    gender,
    email,
    emergencyContact,
    username,
    phoneNo,
    adminActionDate,
    joinDate,
    date,
  }) =>
  async (dispatch) => {
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };
    const body = JSON.stringify({
      name,
      startDate,
      endDate,
      halfDay,
      leaveCategory,
      leaveDescription,
      userid,
      manager,
      employee,
      gender,
      email,
      emergencyContact,
      username,
      phoneNo,
      adminActionDate,
      joinDate,
      date,
    });
    try {
      const res = await axios.post(LEAVE_URL, body, config);
      dispatch({
        type: ADD_LEAVE,
        payload: {
          data: res,
          error: null
        },
      });
    } catch (err) {
      const errors = err.response.data.errors;

      if (errors) {
        errors.forEach((error) => dispatch(setAlert(error.msg, 'danger')));
        dispatch({
          type: ADD_LEAVE,
          payload: {
            data: null,
            error: errors
          },
        });
      }
    }
  };
export const clearState = () => async (dispatch) => {
  dispatch({
    type: ADD_LEAVE,
    payload: [],
  });
};
export const updateLeave = (a, id) => async (dispatch) => {
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  try {
    const res = await axios.patch(`${LEAVE_URL}/${id}`, a, config);

    dispatch({
      type: UPDATE_LEAVE,
      payload: res.data,
    });
  } catch (err) {
    const errors = err.response.data.errors;

    if (errors) {
      errors.forEach((error) => dispatch(setAlert(error.msg, 'danger')));
    }
  }
};
export const deleteLeave = (id) => async (dispatch) => {
  const config = {
    headers: {
      'Content-Type': 'application/json',
    },
  };
  try {
    await axios.delete(`${LEAVE_URL}/${id}`, config, id);
    dispatch({
      type: DELETE_LEAVE,
      payload: id,
    });
  } catch (err) {
    const errors = err.response.data.errors;
    if (errors) {
      errors.forEach((error) => dispatch(setAlert(error.msg, 'danger')));
    }
  }
};
