import { ADD_LEAVE_POLICY, GET_LEAVE_POLICY } from "../actions/adminLeavePolicy";

function adminPolicy(leavePolicy = [], action) {
    const { type, payload } = action;
    switch (type) {
        case GET_LEAVE_POLICY:
            return payload;
        case ADD_LEAVE_POLICY:
            return [payload, ...leavePolicy]
        default:
            return leavePolicy;
    }
}
export default adminPolicy;