import React, { useState, useEffect } from 'react';
import Alert from '../../layout/Alert';
import { useDispatch } from 'react-redux';
import { updateLeavePolicy } from '../../../actions/adminPolicyAction';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import { LEAVE_POLICY_URL } from '../../../apis/apiUrls';
import LoadingSpinner from '../../LoadingSpinner';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
    background: '#F5F5F5'
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      //you want this to be the same as the backgroundColor above
      backgroundColor: '#0EA900',
    },
  },
  background: '#F5F5F5'
}));
const UpdateLeavePolicy = () => {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [showLoader, setShowLoader] = useState(false)
  const [formData, setFormData] = useState({
    title: '',
    content: '',
  });
  const navigate = useNavigate();
  const { id } = useParams();
  const { title, content } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async (e) => {
    setShowLoader(true)
    setTimeout(() => setShowLoader(false), 1000)
    e.preventDefault();
    dispatch(updateLeavePolicy({ title, content, _id: id }));
    navigate('/managePolicy');
  };
  const getLeaveById = async (id) => {
    await axios.get(`${LEAVE_POLICY_URL}/${id}`).then((res) => {
      setFormData(res.data);
    });
  };
  useEffect(() => {
    getLeaveById(id);
  }, [id]);

  return (
    <>
      <div className={classes.mainContainer}>
      {showLoader === true ? <LoadingSpinner></LoadingSpinner> :
        <div
          style={{
            background: '#FFFFFF',
            boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
            borderRadius: '20px',
            width: '100%',
            marginTop: '0.5%',
            padding: '15px',
          }}
        >
         <Grid
              container
              xs={12}
              lg={6}
              spacing={2}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <h3 className="title">Update Policy</h3>
            </Grid>
            <Grid
              container
              xs={12}
              spacing={3}
              style={{ marginTop: '2%', marginRight: 'auto' }}
            >
              <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Policy Title</label>
                <input
                  id="standard-basic"
                  label="Standard"
                  type='text'
                  name="title"
                  className="inputstyle"
                  onChange={(e) => onChange(e)}
                  value={title}
                  fullWidth
                />
              </div>
              </Grid>
              <Grid item lg={12} xs={12} md={12} >
              <div>
                <label htmlFor="">Policy Content (Description)</label>
                <textarea
                  id="standard-basic"
                  label="Standard"
                  name="content"
                  className="inputstyle"
                  onChange={(e) => onChange(e)}
                  value={content}
                  fullWidth
                  style={{height: '200px'}}
                />
              </div>
              </Grid>
            <Alert />
              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >
            <Button
              variant="contained"
              className={classes.btn}
              color="secondary"
              onClick={onSubmit}
            >
              Update
            </Button>
          </Grid>
          </Grid>
        </div>
      }
      </div>
    </>
  );
};
export default UpdateLeavePolicy;
