import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Button from '@material-ui/core/Button';
import TablePagination from '@material-ui/core/TablePagination';
import { useLocation } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: '90%',
  },
  delete: {
    color: '#ff0011',
  },
  button1: {
    width: 20,
    height: 20,
    padding: 0,
  },
  button2: {
    width: 40,
    height: 20,
    padding: 0,
  },
  icon: {
    width: 64,
    height: 64,
  },
  MuiTableRowroot: {
    fontWeight: 'bold !important',
    fontSize: '17px !important',
  },
  btn: {
    background: '#FFA80C',
    color: '#fff',
    width: '82px',
    height: '30px',
    fontSize: '14px !important',
    '&:hover': {
      background: 'rgba(6, 189, 2, 0.2)',
      color: '#06BD02',
    },
    boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
    borderRadius: '32px',
    padding: '4px, 2px, 4px, 2px',
    gap: '10px',
    alignItems: 'center',
    justifyContent: 'center',
    display: 'flex'
  },
}));

function AllLeavetable({ 
  data,
  setSize,
  setPageNumber,
  totalElements,
  setSearchKeyword,
  searchKeyword, }) {
  const classes = useStyles();
  const navigate = useNavigate();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  const location = useLocation();
  const handleChangePage = (event, newPage) => {
    setPageNumber(newPage);
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPageNumber(0);
    setPage(0);
    setRowsPerPage(event.target.value);
    setSize(event.target.value);
  };
  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, totalElements - page * rowsPerPage);
  
    const labelDisplayedRows = ({ from, to, count }) => {
      return `Showing ${from}-${to} of ${count} entries`;
    };  
  return (
    <Grid
      style={{
        marginLeft: 'auto',
        marginRight: 'auto',
      }}
      xs={11}
    >
      <br />
      <h3 className='title'>
        {location.pathname === '/admindashboard'
          ? 'Latest Leave Applications'
          : location.pathname === '/pendingleave'
          ? 'Pending Leave'
          : location.pathname === '/rejectedleave'
          ? 'Rejected Leave'
          : location.pathname === '/grantedleave'
          ? 'Granted Leave'
          : 'Leave History'}
      </h3>
      <br />
      <Grid
        spacing={1}
        container
        xs={12}
        style={{ display: 'flex', justifyContent: 'space-between' }}
      >
        <Grid item lg={9} xs={12} md={9} sm={12}>
          {' '}
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            className="page"
            count={totalElements}
            style={{
              display: 'flex',
              width: '100% !important',
            }}
            rowsPerPage={rowsPerPage}
            labelDisplayedRows={labelDisplayedRows}
            page={page}
            SelectProps={{
              SelectDisplayProps: {
                style: { alignSelf: 'flex-end' },
              },
            }}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Grid>
        <Grid item lg={3} xs={12} md={4} sm={12}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <input
              onChange={(e) => setSearchKeyword(e.target.value)}
              value={searchKeyword}
              id="standard-basic"
              placeholder="Search"
              label="Standard"
              name="search"
              className="inputstyle-two"
              fullWidth  
            />
            <div class="icon-container">
              <img
                src="/search.svg"
                alt=""
                style={{
                  width: '22px',
                  marginLeft: '-50px',
                }}
              />
            </div>
          </div>
        </Grid>
      </Grid>
      <br />
      <TableContainer
        className={classes.rootTable}
        style={{ 
          marginLeft: 'auto',
          marginRight: 'auto',
          background: '#FFFFFF',
          boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
          borderRadius: '20px',
          width: '100%',
       }}
        
      >
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Emp's Name
              </TableCell>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Leave Type
              </TableCell>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Posting Date
              </TableCell>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Status
              </TableCell>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Action
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {data?.map((row) => (
                <TableRow key={row._id}>
                  <TableCell
                    component="th"
                    scope="row"
                    style={{ borderBottom: 'none' }}
                  >
                    {row.name}
                  </TableCell>
                  <TableCell style={{ borderBottom: 'none' }}>
                    {row.leaveCategory}
                  </TableCell>
                  <TableCell style={{ borderBottom: 'none' }}>
                    {row.date}
                  </TableCell>
                  <TableCell
                   style={{ borderBottom: 'none' }}
                  >
                    <div style={{
                      color:
                        row.status === 'Pending'
                          ? '#FFA80C'
                          : row.status === 'Granted'
                          ? '#06BD02'
                          : '#D90101',
                      background:
                        row.status === 'Pending'
                          ? 'rgba(255, 168, 12, 0.2)'
                          : row.status === 'Granted'
                          ? 'rgba(6, 189, 2, 0.2)'
                          : 'rgba(217, 1, 1, 0.2)',
                      boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
                      borderRadius: '51px',
                      padding: '4px, 2px, 4px, 2px',
                      gap: '10px',
                      alignItems: 'center',
                      justifyContent: 'center',
                      display: 'flex',
                      width: '113px',
                      position: 'relative',
                      height: '30px',
                    }}>
                    {row.status}
                    </div>
                  </TableCell>

                  <TableCell style={{ borderBottom: 'none' }}>
                    <Button
                      className={classes.btn}
                      style={{ textTransform: 'none', fontSize: '8px' }}
                      onClick={() => navigate(`/leavedetails/${row._id}`)}
                    >
                      View
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            {emptyRows > 0 && (
              <TableRow style={{ height: 53 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  );
}

export default AllLeavetable;
