import React, { useEffect } from 'react';
import Alert from '../../layout/Alert';
import { useSelector, connect } from 'react-redux';
import { loadUser, updatepassword } from '../../../actions/./authAction/auth';
import { makeStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import PropTypes from 'prop-types';
import './SetPassword.css';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { Helmet } from 'react-helmet';
import { Navigate, useNavigate, useParams } from 'react-router-dom';
import store from '../../../store';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '100%',
    borderRadius: '0px',
    background: '#F5F5F5',
    marginTop: '0.5%',
  },
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
  btn: {
    background: '#0EA900',
    '&:hover': {
      //you want this to be the same as the backgroundColor above
      backgroundColor: '#0EA900',
    },
  },
}));
const SetPassword = ({updatepassword, isAuthenticated, role, verified}) => {
  const alert = useSelector((state) => state.alert);
  const classes = useStyles();
  const navigate = useNavigate();
  const { username } = useParams();

  const formik = useFormik({
    initialValues: {
      currentPassword: '',
      password: '',
      password2: '',
    },
    validationSchema: Yup.object({
      currentPassword: Yup.string()
        .min(6, 'Current password should be 6 digit or more')
        .required('Current password is required'),
      password: Yup.string()
        .min(6, 'Password should be 6 digit or more')
        .required('Password is required'),
      password2: Yup.string()
        .required('Confirm password is required')
        .min(6, 'Confirm password should be 6 digit or more')
        .oneOf([Yup.ref('password'), null], 'Password must match'),
    }),
    onSubmit: (values) => {
      if (alert?.length === 0) {
        let password = values.password;
        let currentPassword = values.currentPassword;
        updatepassword({password, currentPassword, username })
      }
    },
  });

  useEffect(() => {
    store.dispatch(loadUser());
  }, [role, verified]);
  if (isAuthenticated) {
    if (role === 'admin') {
      if (verified === false) {
        navigate(`/setpassword/${username}`)
      }else{
        return <Navigate to="/admindashboard" />;
      }
    } else if (verified === false) {
      navigate(`/setpassword/${username}`)
    }else {
      return <Navigate to="/myprofile" />;
    }
  }
  return (
    <>
      <Helmet>
        <title>Change Password</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <div
          style={{
              background: '#FFFFFF',
              boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
              borderRadius: '20px',
              width: '100%',
              padding: '15px',
  
          }}
        >
                      <form noValidate autoComplete="off" onSubmit={formik.handleSubmit}>
            <Grid
              container
              style={{ marginLeft: 'auto', marginRight: 'auto' }}
              xs={12}
              spacing={3}
            >
              <Grid item xs={12}>
                <h3 className='title'>Change Password </h3>
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
              <div>
                <label htmlFor="">Current Password</label>
                <input
                  id="standard-basic"
                  label="Standard"
                  name="currentPassword"
                  fullWidth
                  placeholder="Enter Current Password"
                  className={
                    formik.touched.currentPassword &&
                    formik.errors.currentPassword
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.currentPassword}
                  type="password"
                />
              </div>
              {formik.touched.currentPassword &&
              formik.errors.currentPassword ? (
                <div style={{ color: 'red' }}>
                  {formik.errors.currentPassword}
                </div>
              ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
              <div>
                <label htmlFor="">New Password</label>
                <input
                  id="standard-basic"
                  label="Standard"
                  placeholder="Enter New Password"
                  name="password"
                  className={
                    formik.touched.password && formik.errors.password
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.password}
                  type="password"
                  fullWidth
                />
              </div>
              {formik.touched.password && formik.errors.password ? (
                <div style={{ color: 'red' }}>{formik.errors.password}</div>
              ) : null}
              </Grid>
              <Grid item lg={6} xs={12} md={6}>
              <div>
                <label htmlFor="">Confirm Password</label>
                <input
                  id="standard-basic"
                  placeholder="Enter Confirm Password"
                  name="password2"
                  minLength="6"
                  className={
                    formik.touched.password2 && formik.errors.password2
                      ? 'inputstyleTwo'
                      : 'inputstyle'
                  }
                  onChange={formik.handleChange}
                  value={formik.values.password2}
                  label="Standard"
                  type="password"
                  fullWidth
                />
                {formik.touched.password2 && formik.errors.password2 ? (
                  <div style={{ color: 'red' }}>{formik.errors.password2}</div>
                ) : null}
              </div>
              </Grid>
              <Alert />
              <Grid
                    item
                    lg={12}
                    md={12}
                    xs={12}
                    sm={12}
                    style={{ display: 'flex', justifyContent: 'flex-end' }}
                  >

              <Button
                variant="contained"
                className={classes.btn}
                color="secondary"
                type="submit"
                disabled={alert.length > 0 ? true : false}
              >
                Change
              </Button>
              </Grid>
              </Grid>
            </form>

        </div>
      </div>
    </>
  );
};

SetPassword.propTypes = {
  updatepassword: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  role: state.auth.role,
  verified: state.auth.verified,
});

export default connect(mapStateToProps, { updatepassword })(SetPassword);
