import React, { useCallback, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AllLeavetable from './AllLeavetable';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { LEAVE_URL } from '../../../apis/apiUrls';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
    background: '#F5F5F5'
  },
}));

function AllLeave() {
  const location = useLocation();
  const classes = useStyles();
  const [searchKeyword, setSearchKeyword] = useState('');
  const [data, setData] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [size, setSize] = useState(5);
  const [totalElements, setTotalElements] = useState(0);
  const getLeaves = useCallback(
    async (p, s) => {
      axios.get(`${LEAVE_URL}?keyword=${searchKeyword}&page=${p}&size=${s}`).then((users) => {
          setData(users?.data?.content);
          setTotalElements(users?.data?.totalElements);
      });
    },
    [searchKeyword]
  );
  useEffect(() => {
    getLeaves(pageNumber, size);
  }, [pageNumber, size, getLeaves]);
  return (
    <>
      <Helmet htmlAttributes>
        <title>
          {location.pathname === '/admindashboard' ? 'Dashboard' : 'All Leaves'}
        </title>
      </Helmet>
      <div
        className={classes.mainContainer}
        style={{ width: location.pathname === '/admindashboard' ? '100%' : '' }}
      >
        <div
          style={{
            minHeight: '59vh',
            width: '100%',
            borderRadius: '0px',
            // marginTop: '0.5%',
          }}
        >
          <AllLeavetable  
            setSize={setSize}
            setSearchKeyword={setSearchKeyword}
            searchKeyword={searchKeyword}
            setPageNumber={setPageNumber}
            data={data}
            totalElements={totalElements} />
        </div>
      </div>
    </>
  );
}

export default AllLeave;
