import React, { useState, useEffect, useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import axios from 'axios';
import EmployeeTable from './EmployeeTable';
import { LOAD_ALL_USER_URL } from '../../../apis/apiUrls';
import { Helmet } from 'react-helmet';
import { connect } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  mainContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    width: '90%',
    height: '100%',
    marginLeft: 'auto',
    marginTop: '2%',
    marginRight: 'auto',
    background: '#F5F5F5',
    [theme.breakpoints.down('xs')]: {
      marginTop: '20%',
    },
  },
}));

function ManageEmployee({auth: { user }}) {
  const classes = useStyles();

  const [searchKeyword, setSearchKeyword] = useState('');

  const [data, setData] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [size, setSize] = useState(5);

  const [totalElements, setTotalElements] = useState(0);
  
  const getUsers = useCallback(
    async (p, s) => {
      if(user.role==='admin'){
        axios.get(`${LOAD_ALL_USER_URL}?keyword=${searchKeyword}&page=${p}&size=${s}`).then((users) => {
          setData(users?.data?.content);
          setTotalElements(users?.data?.totalElements);
        });
      }else{
        axios.get(`${LOAD_ALL_USER_URL}?keyword=${searchKeyword}&page=${p}&size=${s}&manager=${user._id}`).then((users) => {
          setData(users?.data?.content);
          setTotalElements(users?.data?.totalElements);
        });
      }
    },
    [searchKeyword, user]
  );

  useEffect(() => {
    getUsers(pageNumber, size);
  }, [pageNumber, size, getUsers]);

  return (
    <>
      <Helmet htmlAttributes>
        <title>Manage Employee</title>
      </Helmet>
      <div className={classes.mainContainer}>
        <div
          style={{
            minHeight: '59vh',
            width: '100%',
            borderRadius: '0px',
            background: '#F5F5F5',
            marginTop: '0.5%',
          }}
        >
          {' '}
          <br />
          <EmployeeTable
            setSize={setSize}
            setSearchKeyword={setSearchKeyword}
            searchKeyword={searchKeyword}
            setPageNumber={setPageNumber}
            data={data}
            totalElements={totalElements}
            setData={setData}
            setTotalElements={setTotalElements}
          />
        </div>
      </div>
    </>
  );
}
const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps)(ManageEmployee);
