import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import DeleteIcon from '@mui/icons-material/Delete';
import LaunchIcon from '@mui/icons-material/Launch';
import IconButton from '@material-ui/core/IconButton';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import TablePagination from '@material-ui/core/TablePagination';

const useStyles = makeStyles({
  table: {
    minWidth: '90%',
  },
  delete: {
    color: '#ff0011',
    //   marginLeft:45
  },
  button1: {
    width: 20,
    height: 20,
    padding: 0,
  },
  button2: {
    width: 40,
    height: 20,
    padding: 0,
  },
  icon: {
    width: 64,
    height: 64,
  },
  MuiTableRowroot: {
    fontWeight: 'bold !important',
    fontSize: '17px !important',
  },
});

function LeavePolicyTable({ 
  data,
  setSize,
  setPageNumber,
  totalElements,
  setSearchKeyword,
  searchKeyword,
  HandleDeleteLeavePolicy }) {
    const classes = useStyles();
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);
  
    const handleChangePage = (event, newPage) => {
      setPageNumber(newPage);
      setPage(newPage);
    };
  
    const handleChangeRowsPerPage = (event) => {
      setPageNumber(0);
      setPage(0);
      setRowsPerPage(event.target.value);
      setSize(event.target.value);
    };
    const emptyRows =
      rowsPerPage - Math.min(rowsPerPage, totalElements - page * rowsPerPage);
  const HandleDelete = (id) => {
    HandleDeleteLeavePolicy(id);
  };
  const labelDisplayedRows = ({ from, to, count }) => {
    return `Showing ${from}-${to} of ${count} entries`;
  }; 
  return (
    <Grid
      style={{
        marginLeft: 'auto',
        marginRight: 'auto',
      }}
      xs={11}
    >
      <h3 className='title'>Manage Policy </h3>
      <br />

      <Grid
        spacing={1}
        container
        xs={12}
        style={{ display: 'flex', justifyContent: 'space-between' }}
      >
        <Grid item lg={9} xs={12} md={9} sm={12}>
          {' '}
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            className="page"
            count={totalElements}
            style={{
              display: 'flex',
              width: '100% !important',
            }}
            rowsPerPage={rowsPerPage}
            labelDisplayedRows={labelDisplayedRows}
            page={page}
            SelectProps={{
              SelectDisplayProps: {
                style: { alignSelf: 'flex-end' },
              },
            }}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Grid>
        <Grid item lg={3} xs={12} md={4} sm={12}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <input
              onChange={(e) => setSearchKeyword(e.target.value)}
              value={searchKeyword}
              id="standard-basic"
              placeholder="Search"
              label="Standard"
              name="search"
              className="inputstyle-two"
              fullWidth  
            />
            <div class="icon-container">
              <img
                src="/search.svg"
                alt=""
                style={{
                  width: '22px',
                  marginLeft: '-50px',
                }}
              />
            </div>
          </div>
        </Grid>
      </Grid>
      <br />
      <TableContainer
        className={classes.rootTable}
        style={{ 
          marginLeft: 'auto',
          marginRight: 'auto',
          background: '#FFFFFF',
          boxShadow: '-1px 0px 20px 1px rgba(0, 0, 0, 0.1)',
          borderRadius: '20px',
          width: '100%',
       }}
      >
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Title
              </TableCell>
              <TableCell
                className={classes.MuiTableRowroot}
                style={{ borderBottom: '2px solid gray' }}
              >
                Action
              </TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {data?.map((row) => (
                <TableRow key={row._id}>
                  <TableCell
                    component="th"
                    scope="row"
                    style={{ borderBottom: 'none' }}
                  >
                    {row.title}
                  </TableCell>
                  <TableCell style={{ borderBottom: 'none' }}>
                    <div style={{ display: 'flex', justifyContent: 'start' }}>
                      <IconButton
                        className={classes.button1}
                        component={Link}
                        to={`/updateLeavePolicy/${row._id}`}
                      >
                        <LaunchIcon />
                      </IconButton>
                      <IconButton
                        className={classes.button2}
                        onClick={() => HandleDelete(row._id)}
                      >
                        <DeleteIcon className={classes.delete} />
                      </IconButton>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            {emptyRows > 0 && (
              <TableRow style={{ height: 53 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Grid>
  );
}

export default LeavePolicyTable;
